/**
 * WordPress dependencies
 */
import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'

addFilter( 'stackable.team-member.mainclasses_1_17_3', 'stackable/team-member', ( classes, design, props ) => {
	const { customCSSUniqueID = '' } = props.attributes
	return {
		...classes,
		[ customCSSUniqueID ]: customCSSUniqueID,
	}
} )

addFilter( 'stackable.team-member.mainclasses_1_17_3', 'stackable/team-member', ( classes, design, props ) => {
	const {
		colorOnHover = false,
	} = props.attributes
	return {
		...classes,
		'ugb-team-member--color-on-hover': colorOnHover,
	}
} )

addFilter( 'stackable.team-member.save.output.before_1_17_3', 'stackable/team-member', ( output, design, props ) => {
	const { customCSSCompiled = '' } = props.attributes
	if ( ! customCSSCompiled ) {
		return output
	}

	return (
		<Fragment>
			{ output }
			<style>{ customCSSCompiled }</style>
		</Fragment>
	)
} )

addFilter( 'stackable.team-member.itemclasses_1_17_3', 'stackable/team-member', ( classes, design, i, props ) => {
	if ( [ 'overlay', 'horizontal', 'half' ].includes( design ) ) {
		const {
			shadow = 3,
		} = props
		return {
			...classes,
			[ `ugb--shadow-${ shadow }` ]: true,
		}
	}
	return classes
} )

addFilter( 'stackable.team-member.itemstyles_1_17_3', 'stackable/team-member', ( styles, design, i, props ) => {
	if ( [ 'overlay', 'horizontal', 'half' ].includes( design ) ) {
		const {
			borderRadius = 12,
		} = props.attributes
		return {
			...styles,
			item: {
				...styles.item,
				borderRadius: borderRadius ? borderRadius : undefined,
			},
		}
	}
	return styles
} )

addFilter( 'stackable.team-member.save.output_1_17_3', 'stackable/team-member', ( output, design, comps, i, props ) => {
	const mediaURL = props.attributes[ `mediaURL${ i }` ]
	const imageStyles = {
		backgroundImage: mediaURL ? `url(${ mediaURL })` : undefined,
	}

	if ( design === 'overlay' || design === 'half' ) {
		return (
			<Fragment>
				<div className="ugb-team-member__image" style={ imageStyles } data-src={ mediaURL } />
				<div className="ugb-team-member__content">
					{ comps.nameComp }
					{ comps.positionComp }
					{ comps.descriptionComp }
				</div>
			</Fragment>
		)
	} else if ( design === 'horizontal' ) {
		return (
			<Fragment>
				{ comps.imageComp }
				<div className="ugb-team-member__content">
					{ comps.nameComp }
					{ comps.positionComp }
					{ comps.descriptionComp }
				</div>
			</Fragment>
		)
	}
	return output
} )
