/**
 * WordPress dependencies
 */
import { addFilter } from '@wordpress/hooks'

/**
 * External dependencies
 */
import deepmerge from 'deepmerge'
import { range } from 'lodash'
import {
	isDarkColor,
	createImageMask,
	appendImportant,
} from '~stackable/util'
import {
	createBackgroundStyleSet,
} from '~stackable/deprecated/v2/util'
import { showOptions } from '~stackable/deprecated/v2/block/feature-grid/util'

/**
 * Internal dependencies
 */
import { notMidLargeEdgeColumn } from '.'

addFilter( 'stackable.feature-grid.styles', 'stackable/premium/feature-grid', ( styles, props ) => {
	const newStyles = [ styles ]

	const {
		design = 'basic',
		columns = 3,
		showImage = true,
	} = props.attributes

	const show = showOptions( props )

	// Overridden colors.
	range( 1, columns + 1 ).forEach( i => {
		const columnBackgroundColor = props.attributes[ `column${ i }BackgroundColor` ]

		if ( show.columnBackground && columnBackgroundColor && notMidLargeEdgeColumn( design, i, columns ) ) {
			newStyles.push( {
				[ `.ugb-feature-grid__item.ugb-feature-grid__item${ i }` ]: {
					backgroundColor: appendImportant( columnBackgroundColor ),
				},
				[ `.ugb-feature-grid__item.ugb-feature-grid__item${ i }:before` ]: {
					display: 'none',
				},
			} )
		}
		if ( show.columnBackground && columnBackgroundColor ) {
			newStyles.push( {
				[ `.ugb-feature-grid__item.ugb-feature-grid__item${ i } .ugb-feature-grid__title, ` +
				  `.ugb-feature-grid__item.ugb-feature-grid__item${ i } .ugb-feature-grid__description` ]: {
					color: isDarkColor( columnBackgroundColor ) ? '#ffffff' : '#222222',
				},
			} )
		}
	} )

	// Advanced image.
	if ( showImage ) {
		range( 1, columns + 1 ).forEach( i => {
			if ( props.attributes[ `image${ i }Shape` ] || props.attributes.imageShape ) {
				newStyles.push( {
					[ `.ugb-feature-grid__item${ i } .ugb-img` ]: createImageMask( `image${ i }%s`, props.attributes, { parentAttrNameTemplate: `image%s` } ),
				} )
			}
		} )
	}

	// Create background styles for only a few columns for the large-mid layout.
	if ( design === 'large-mid' ) {
		if ( columns >= 2 ) {
			newStyles.push( {
				...createBackgroundStyleSet( 'column%s', 'ugb-feature-grid__item2', props.attributes, {
					importantBackgroundColor: true,
				} ),
			} )
		}
		if ( columns >= 4 ) {
			newStyles.push( {
				...createBackgroundStyleSet( 'column%s', 'ugb-feature-grid__item3', props.attributes, {
					importantBackgroundColor: true,
				} ),
			} )
		}
	}

	return newStyles.length === 1 ? newStyles[ 0 ] : deepmerge.all( newStyles )
}, 11 )

// Don't apply column background colors for the sides for the large-mid design.
addFilter( 'stackable.feature-grid.styles.column-background', 'stackable/feature-grid', ( origSelector, attributes ) => {
	return attributes.design !== 'large-mid' ? origSelector : ''
} )
