
/**
 * WordPress dependencies
 */
import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'

addFilter( 'stackable.button.mainclasses_1_15_5', 'stackable/button', ( classes, design, props ) => {
	const { customCSSUniqueID = '' } = props.attributes
	return {
		...classes,
		[ customCSSUniqueID ]: customCSSUniqueID,
	}
} )

addFilter( 'stackable.button.save.output.after_1_15_5', 'stackable/button', ( output, design, props ) => {
	const { customCSSCompiled = '' } = props.attributes
	if ( ! customCSSCompiled ) {
		return output
	}

	return (
		<Fragment>
			{ output }
			<style>{ customCSSCompiled }</style>
		</Fragment>
	)
}, 11 )
