
/**
 * WordPress dependencies
 */
import {
	addAction, addFilter, applyFilters,
} from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'

/**
 * External dependencies
 */
import { isPro } from 'stackable'

/**
 * Internal dependencies
 */
import PanelCustomCSSSettings from '../../../../components/panel-custom-css-settings'

const customCSSInspector = ( output, props ) => {
	const { setAttributes } = props
	const {
		customCSS = '',
	} = props.attributes

	return (
		<Fragment>
			{ output }
			<PanelCustomCSSSettings
				value={ customCSS }
				defaultCSS={ applyFilters( `stackable.${ props.blockName }.custom-css.default`, '' ) }
				onChange={ customCSS => setAttributes( { customCSS } ) }
			/>
		</Fragment>
	)
}

/**
 * Outputs rendered styles.
 *
 * @param {boolean} isEditor True if we're rendering styles for the editor only.
 */
const outputStyle = ( isEditor = false ) => ( output, design, props ) => {
	const {
		customCSS = '',
		uniqueClass = props.attributes.customCSSUniqueID || '',
	} = props.attributes

	const CSSSettings = PanelCustomCSSSettings[ isEditor ? 'Edit' : 'Content' ]

	return (
		<Fragment>
			{ output }
			<CSSSettings
				value={ customCSS }
				defaultCSS={ applyFilters( `stackable.${ props.blockName }.custom-css.default`, '' ) }
				uniqueClass={ uniqueClass }
				mainClass={ props.mainClassName }
				isEditor={ isEditor }
			/>
		</Fragment>
	)
}

addAction( `stackable.module.custom-css`, 'stackable/pro/module/custom-css', blockName => {
	if ( isPro ) {
		addFilter( `stackable.${ blockName }.edit.inspector.advanced.after`, `stackable/pro/${ blockName }/custom-css`, customCSSInspector, 20 )
	}
	addFilter( `stackable.${ blockName }.edit.output.outer`, `stackable/pro/${ blockName }/custom-css`, outputStyle( true ) )
	addFilter( `stackable.${ blockName }.save.output.outer`, `stackable/pro/${ blockName }/custom-css`, outputStyle() )
} )
