
/**
 * Internal dependencies
 */
import './custom-css'
import './deprecated'
import './style'

/**
 * WordPress dependencies
 */
import { addFilter } from '@wordpress/hooks'

addFilter( 'stackable.accordion.itemclasses', 'stackable/accordion', ( classes, props ) => {
	const {
		design = 'basic',
		shadow = '',
	} = props.attributes

	return {
		...classes,
		[ `ugb--shadow-${ shadow || 3 }` ]: design === 'line-colored' || design === 'colored',
	}
} )
