/**
 * WordPress dependencies
 */
import { addFilter } from '@wordpress/hooks'

/**
 * External dependencies
 */
import deepmerge from 'deepmerge'
import {
	appendImportant,
	createImageBackgroundStyleSet,
	__getValue,
} from '~stackable/util'

addFilter( 'stackable.container.styles', 'stackable/premium/container', ( styles, props ) => {
	const getValue = __getValue( props.attributes )

	const newStyles = [ styles ]

	const {
		uniqueClass = '',
		design = 'basic',
		imageHeightUnit = 'px',
		imageTabletHeightUnit = 'px',
		imageMobileHeightUnit = 'px',
		imageWidthUnit = '%',
		imageTabletWidthUnit = '%',
		imageMobileWidthUnit = '%',
	} = props.attributes

	if ( [ 'image', 'image2', 'image3' ].includes( design ) ) {
		newStyles.push( {
			...createImageBackgroundStyleSet( 'image%s', `${ uniqueClass }-wrapper > .ugb-container__image`, props.attributes ),
		} )
	}
	if ( design === 'image' ) {
		newStyles.push( {
			desktopTablet: {
				[ `.${ uniqueClass }-wrapper > .ugb-container__image` ]: {
					height: appendImportant( getValue( 'imageHeight', '%s' ) ? getValue( 'imageHeight', '%s' ) + imageHeightUnit : undefined ),
				},
			},
			tabletOnly: {
				[ `.${ uniqueClass }-wrapper > .ugb-container__image` ]: {
					height: appendImportant( getValue( 'imageTabletHeight', '%s' ) ? getValue( 'imageTabletHeight', '%s' ) + imageTabletHeightUnit : undefined ),
				},
			},
			mobile: {
				[ `.${ uniqueClass }-wrapper > .ugb-container__image` ]: {
					height: appendImportant( getValue( 'imageMobileHeight', '%s' ) ? getValue( 'imageMobileHeight', '%s' ) + imageMobileHeightUnit : undefined ),
				},
			},
		} )
	}
	if ( design === 'image2' || design === 'image3' ) {
		newStyles.push( {
			[ `.${ uniqueClass }-wrapper > .ugb-container__image` ]: {
				width: appendImportant( getValue( 'imageWidth', '%s' ) ? getValue( 'imageWidth', '%s' ) + imageWidthUnit : undefined ),
			},
			tablet: {
				[ `.${ uniqueClass }-wrapper > .ugb-container__image` ]: {
					width: appendImportant( getValue( 'imageTabletWidth', '%s' ) ? getValue( 'imageTabletWidth', '%s' ) + imageTabletWidthUnit : undefined ),
				},
			},
			mobile: {
				[ `.${ uniqueClass }-wrapper > .ugb-container__image` ]: {
					width: appendImportant( getValue( 'imageMobileWidth', '%s' ) ? getValue( 'imageMobileWidth', '%s' ) + imageMobileWidthUnit : undefined ),
				},
			},
		} )

		// Collapsed image height on mobile.
		const {
			imageCollapseOnMobile = true,
		} = props.attributes
		if ( imageCollapseOnMobile ) {
			newStyles.push( {
				mobile: {
					[ `.${ uniqueClass }-wrapper > .ugb-container__image` ]: {
						height: appendImportant( getValue( 'imageCollapseOnMobileHeight', '%spx' ) ),
					},
				},
			} )
		}
	}

	return newStyles.length === 1 ? newStyles[ 0 ] : deepmerge.all( newStyles )
}, 11 )
