/**
 * WordPress dependencies
 */
import { addFilter, applyFilters } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'

/**
 * External dependencies
 */
import { omit } from 'lodash'
import classnames from 'classnames'

addFilter( 'stackable.feature.mainclasses_1_17_2', 'stackable/feature', ( classes, design, props ) => {
	const { customCSSUniqueID = '' } = props.attributes
	return {
		...classes,
		[ customCSSUniqueID ]: customCSSUniqueID,
	}
} )

addFilter( 'stackable.feature.save.output.before_1_17_2', 'stackable/feature', ( output, design, props ) => {
	const { customCSSCompiled = '' } = props.attributes
	if ( ! customCSSCompiled ) {
		return output
	}

	return (
		<Fragment>
			{ output }
			<style>{ customCSSCompiled }</style>
		</Fragment>
	)
} )

addFilter( 'stackable.feature.mainclasses_1_17_2', 'stackable/feature', ( classes, design, props ) => {
	const { shadow } = props.attributes
	if ( design === 'overlap' || design === 'overlap2' || design === 'overlap3' ) {
		return omit( classes, [
			'ugb--has-background',
			'ugb--has-background-image',
			`ugb--shadow-${ shadow }`,
			'ugb--has-background-gradient',
			'ugb--has-background-video',
		] )
	}

	return classes
} )

addFilter( 'stackable.feature.mainclasses_1_17_2', 'stackable/feature', ( classes, design, props ) => {
	const { hoverEffect = '' } = props.attributes
	if ( [ 'basic', 'half' ].includes( design ) ) {
		return {
			...classes,
			[ `ugb--hover-${ hoverEffect }` ]: hoverEffect,
		}
	}
	return classes
} )

addFilter( 'stackable.feature.styles_1_17_2', 'stackable/feature', ( styles, design, props ) => {
	const { imageSize, borderRadius } = props.attributes
	if ( design === 'half' ) {
		return {
			...styles,
			image: {
				...styles.image,
				minHeight: imageSize,
			},
		}
	} else if ( design === 'overlap' || design === 'overlap2' || design === 'overlap3' ) {
		return {
			...styles,
			image: {
				...styles.image,
				minHeight: imageSize,
				borderRadius,
			},
			main: omit( styles.main, [
				'backgroundColor',
				'backgroundImage',
				'backgroundAttachment',
				'--ugb-background-color',
				'--ugb-background-color2',
				'--ugb-background-direction',
				'borderRadius',
			] ),
		}
	}
	return styles
} )

addFilter( 'stackable.feature.save.output_1_17_2', 'stackable/feature', ( output, comps, props ) => {
	const {
		design,
		imageUrl,
		imageSize,
		backgroundColorType = '',
		backgroundColor,
		backgroundColor2,
		backgroundColorDirection = 0,
		borderRadius,
		shadow,
	} = props.attributes

	if ( design === 'half' ) {
		const contentClasses = classnames( [
			'ugb-feature__content',
		], applyFilters( 'stackable.feature.contentclasses_1_17_2', {
			[ `ugb--has-background-gradient` ]: backgroundColorType === 'gradient',
		}, design, props ) )
		const contentStyle = {
			backgroundColor: backgroundColorType === 'gradient' ? backgroundColor : undefined,
			'--ugb-background-color': backgroundColorType === 'gradient' ? backgroundColor : undefined,
			'--ugb-background-color2': backgroundColorType === 'gradient' && backgroundColor2 ? backgroundColor2 : undefined,
			'--ugb-background-direction': backgroundColorType === 'gradient' ? `${ backgroundColorDirection }deg` : undefined,
		}
		const imageStyles = {
			backgroundImage: imageUrl ? `url(${ imageUrl })` : undefined,
			minHeight: imageSize,
		}
		return (
			<div className="ugb-content-wrapper">
				<div className={ contentClasses } style={ contentStyle }>
					{ comps.titleComp }
					{ comps.descriptionComp }
					{ comps.buttonComp }
				</div>
				<div className="ugb-feature__image" style={ imageStyles } />
			</div>
		)
	}

	if ( design === 'overlap' || design === 'overlap2' || design === 'overlap3' ) {
		const contentClasses = classnames( [
			'ugb-feature__content',
		], applyFilters( 'stackable.feature.contentclasses_1_17_2', {
			[ `ugb--shadow-${ shadow }` ]: shadow,
			[ `ugb--has-background-gradient` ]: backgroundColorType === 'gradient',
		}, design, props ) )
		const contentStyle = {
			borderRadius,
			backgroundColor: backgroundColor ? backgroundColor : undefined,
			'--ugb-background-color': backgroundColorType === 'gradient' ? backgroundColor : undefined,
			'--ugb-background-color2': backgroundColorType === 'gradient' && backgroundColor2 ? backgroundColor2 : undefined,
			'--ugb-background-direction': backgroundColorType === 'gradient' ? `${ backgroundColorDirection }deg` : undefined,
		}
		const imageStyle = {
			backgroundImage: imageUrl ? `url(${ imageUrl })` : undefined,
			minHeight: imageSize,
			borderRadius,
		}
		return (
			<div className="ugb-content-wrapper">
				<div className={ contentClasses } style={ contentStyle }>
					{ comps.titleComp }
					{ comps.descriptionComp }
					{ comps.buttonComp }
				</div>
				<div className="ugb-feature__image" style={ imageStyle } />
			</div>
		)
	}

	return output
} )
