
/**
 * Internal dependencies
 */
import './custom-css'
import './style'

/**
 * External dependencies
 */
import {
	i18n, isPro,
} from 'stackable'
import {
	AdvancedRangeControl,
	AdvancedToggleControl,
	SortControl,
} from '~stackable/components'
import { ResponsiveControl } from '../../../../../../src/deprecated/v2/components'
import { moveInnerBlock } from '~stackable/util'
import { showOptions } from '~stackable/deprecated/v2/block/columns/util'

/**
 * WordPress dependencies
 */
import {
	__,
} from '@wordpress/i18n'
import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import { select } from '@wordpress/data'

if ( isPro ) {
	addFilter( 'stackable.columns.edit.inspector.columns.after', 'stackable/columns', ( output, props ) => {
		const { setAttributes } = props
		const {
			reverseColumns = '',
		} = props.attributes

		const show = showOptions( props )

		return (
			<Fragment>
				{ output }
				{ show.reverseColumns &&
					<AdvancedToggleControl
						label={ __( 'Reverse columns', i18n ) }
						checked={ reverseColumns }
						onChange={ reverseColumns => setAttributes( { reverseColumns } ) }
					/>
				}
			</Fragment>
		)
	} )

	addFilter( 'stackable.columns.edit.advanced.responsive.after', 'stackable/columns', ( output, props ) => {
		const { setAttributes } = props
		const {
			columns = 2,
			collapsedColumnsOrder = '',
			collapsedRowGap = '',
		} = props.attributes

		return (
			<Fragment>
				{ output }
				<AdvancedRangeControl
					label={ __( 'Collapsed Row Gap', i18n ) }
					min={ 0 }
					max={ 500 }
					allowReset={ true }
					placeholder="35"
					value={ collapsedRowGap }
					onChange={ collapsedRowGap => setAttributes( { collapsedRowGap } ) }
				/>
				<SortControl
					label={ __( 'Collapsed Col. Arrangement', i18n ) }
					axis="y"
					values={ ( collapsedColumnsOrder || '1,2,3,4,5,6' ).split( ',' ) }
					num={ columns }
					onChange={ collapsedColumnsOrder => {
						setAttributes( { collapsedColumnsOrder: collapsedColumnsOrder ? collapsedColumnsOrder.join( ',' ) : '' } )
					} }
					hasReset={ true }
				/>
			</Fragment>
		)
	} )

	addFilter( 'stackable.columns.edit.inspector.columns.after', 'stackable/columns', ( output, props ) => {
		const { setState } = props
		const {
			columns = 2,
		} = props.attributes

		return (
			<Fragment>
				{ output }
				<SortControl
					num={ columns }
					onChange={ ( order, { oldIndex, newIndex } ) => {
						moveInnerBlock( select( 'core/block-editor' ).getSelectedBlockClientId(), oldIndex, newIndex )
					} }
					onHover={ sortColumnHighlight => setState( { sortColumnHighlight } ) }
					className="ugb--help-tip-column-arrangement"
				/>
			</Fragment>
		)
	} )

	addFilter( 'stackable.columns.edit.inspector.style.column-widths.after', 'stackable/columns', ( output, props ) => {
		const { setAttributes } = props

		const show = showOptions( props )

		return (
			<Fragment>
				{ output }
				<ResponsiveControl
					attrNameTemplate="%sColumnGap"
					setAttributes={ setAttributes }
					blockAttributes={ props.attributes }
				>
					<AdvancedRangeControl
						label={ __( 'Column Gap', i18n ) }
						min={ 0 }
						max={ 500 }
						allowReset={ true }
						placeholder="35"
						className="ugb--help-tip-advanced-column-gap"
					/>
				</ResponsiveControl>

				{ show.rowGap &&
					<ResponsiveControl
						attrNameTemplate="%sRowGap"
						setAttributes={ setAttributes }
						blockAttributes={ props.attributes }
					>
						<AdvancedRangeControl
							label={ __( 'Row Gap', i18n ) }
							min={ 0 }
							max={ 500 }
							allowReset={ true }
							placeholder="35"
							className="ugb--help-tip-column-row-gap"
						/>
					</ResponsiveControl>
				}
			</Fragment>
		)
	} )
}
