/**
 * WordPress dependencies
 */
import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'

/**
 * External dependencies
 */
import { isDarkColor } from '~stackable/util'

addFilter( 'stackable.blockquote.mainclasses_1_17_3', 'stackable/blockquote', ( classes, design, props ) => {
	const { customCSSUniqueID = '' } = props.attributes
	return {
		...classes,
		[ customCSSUniqueID ]: customCSSUniqueID,
	}
} )

addFilter( 'stackable.blockquote.mainclasses_1_17_3', 'stackable/blockquote', ( classes, design, props ) => {
	const { color } = props.attributes
	if ( design === 'highlight' ) {
		return {
			...classes,
			'ugb-blockquote--dark-text': color ? isDarkColor( color ) : undefined,
		}
	}
	return classes
} )

addFilter( 'stackable.blockquote.styles_1_17_3', 'stackable/blockquote', ( styles, design, props ) => {
	const { color } = props.attributes
	if ( design === 'highlight' ) {
		return {
			...styles,
			main: {
				...styles.main,
				'--ugb-box-color': color,
			},
		}
	}
	return styles
} )

addFilter( 'stackable.blockquote.save.output.before_1_17_3', 'stackable/blockquote', ( output, design, props ) => {
	const { customCSSCompiled = '' } = props.attributes
	if ( ! customCSSCompiled ) {
		return output
	}

	return (
		<Fragment>
			{ output }
			<style>{ customCSSCompiled }</style>
		</Fragment>
	)
} )

addFilter( 'stackable.blockquote.save.output_1_17_3', 'stackable/blockquote', ( output, design ) => {
	if ( design === 'highlight' ) {
		return (
			<span className="ugb-blockquote__highlight-wrap">
				{ output }
			</span>
		)
	}
	return output
} )
