/**
 * External dependencies
 */
import {
	range, camelCase, clamp,
} from 'lodash'
import classnames from 'classnames'
import { i18n } from 'stackable'

/**
 * WordPress dependencies
 */
import { addFilter } from '@wordpress/hooks'
import { __, sprintf } from '@wordpress/i18n'
import {
	Fragment, useState, useEffect,
} from '@wordpress/element'

/**
 * Function used to create pagination array to avoid crowded buttons.
 * e.g. ( [1,2,3,"...", 11] )
 *
 * @param {number} currentPage
 * @param {number} pages
 *
 * @return {Array} generated pagination array
 */
export const generatePaginationArray = ( currentPage, pages ) => {
	const offsetInBetween = 2
	const leftOffset = clamp( currentPage - offsetInBetween, 1, pages )
	const rightOffset = clamp( currentPage + offsetInBetween, 1, pages )
	const ret = [ 1, '...', ...range( leftOffset, rightOffset + 1 ), '...', pages ]
	if ( leftOffset === 1 ) {
		ret.splice( 0, 2 )
	}
	if ( leftOffset === 2 ) {
		ret.splice( 1, 1 )
	}
	if ( rightOffset === pages ) {
		ret.splice( ret.length - 3, 2 )
	}
	if ( rightOffset === pages - 1 ) {
		ret.splice( ret.length - 3, 1 )
	}
	return ret
}

addFilter( 'stackable.blog-posts.pagination.hooks', 'usePagination', ( posts, numberOfItems ) => {
	const [ pages, setPages ] = useState( 1 )
	const [ currentPage, setCurrentPage ] = useState( 1 )
	const [ currentPagePosts, setCurrentPagePosts ] = useState( [] )

	useEffect( () => {
		setCurrentPagePosts( [ ...( posts || [] ) ].splice( ( currentPage - 1 ) * numberOfItems, numberOfItems ) )
	}, [ currentPage, pages, posts ] )

	useEffect( () => {
		setCurrentPage( 1 )
		setPages( Math.ceil( ( posts || [] ).length / numberOfItems ) )
	}, [ JSON.stringify( posts ), numberOfItems ] )

	return {
		paginate: setCurrentPage,
		currentPage,
		pages,
		currentPagePosts,
	}
} )

addFilter( 'stackable.blog-posts.edit.output.pagination.after', 'pagination', ( output, props ) => {
	const getAttrName = attrName => camelCase( sprintf( props.attrNameTemplate, attrName ) )
	const getAttrValue = ( attrName, defaultValue = '' ) => {
		const value = props.blockAttributes[ getAttrName( attrName ) ]
		return value === 0 ? value : ( value || defaultValue )
	}

	const {
		currentPage = 1,
		pages = 1,
		paginate = () => {},
	} = props

	const containerClassNames = classnames( [
		'ugb-button-container',
		props.containerClassName,
	]
	)

	const mainClasses = classnames( [
		'ugb-button',
		`ugb-button--size-${ getAttrValue( 'Size', 'normal' ) }`,
	], {
		'ugb-button--ghost-to-normal-effect': getAttrValue( 'HoverGhostToNormal' ),
		[ `ugb--hover-effect-${ getAttrValue( 'HoverEffect' ) }` ]: getAttrValue( 'Design' ) !== 'link' && getAttrValue( 'HoverEffect' ),
		[ `ugb--shadow-${ getAttrValue( 'Shadow' ) }` ]: getAttrValue( 'Design', 'basic' ) === 'basic' && getAttrValue( 'Shadow' ),
		[ `ugb-button--design-${ getAttrValue( 'Design' ) }` ]: getAttrValue( 'Design', 'basic' ) !== 'basic',
	} )

	return (
		<Fragment>
			{ output }
			{ props.showPagination && (
				<div className={ containerClassNames }>
					{ props.attributes.showNextPrevious && currentPage !== 1 &&
						<PaginationButton
							className={ mainClasses }
							onClick={ () => paginate( currentPage - 1 ) }
							title={ __( 'Previous page', i18n ) }
							rel="prev"
						>
							{ props.attributes.previousLabel }
						</PaginationButton> }
					{ generatePaginationArray( currentPage, pages ).map( ( page, index ) => <PaginationButton
						key={ page + index }
						onClick={ () => page !== '...' && paginate( page ) }
						isDot={ page === '...' }
						className={ mainClasses }
						isActive={ currentPage === page }
						title={ page !== '...' ? sprintf( __( 'Page %s', i18n ), page ) : undefined }
					>
						{ page }
					</PaginationButton>
					) }
					{ props.attributes.showNextPrevious && pages !== 1 && currentPage !== pages &&
						<PaginationButton
							className={ mainClasses }
							onClick={ () => paginate( currentPage + 1 ) }
							title={ __( 'Next page', i18n ) }
							rel="next"
						>
							{ props.attributes.nextLabel }
						</PaginationButton>
					}
				</div>
			) }
		</Fragment>
	)
} )

const PaginationButton = ( {
	className, children, onClick, isActive, isDot, rel, title,
} ) => {
	const TagName = isActive ? 'span' : 'button'
	const mainClasses = classnames(
		className, {
			'is-active': isActive,
			dots: isDot,
		}
	)

	return (
		<TagName
			className={ mainClasses }
			onClick={ onClick }
			rel={ rel }
			title={ title }
			aria-current={ isActive ? 'page' : undefined }
			aria-hidden={ isDot ? 'true' : undefined }
		>
			<span className="ugb-button--inner">
				{ children }
			</span>
		</TagName>
	)
}

PaginationButton.defaultProps = {
	className: '',
	children: null,
	onClick: () => {},
	isActive: false,
}

