/**
 * WordPress dependencies
 */
import { addFilter } from '@wordpress/hooks'

/**
 * External dependencies
 */
import {
	appendImportant,
	isDarkColor,
	hexToRgba,
	whiteIfDarkBlackIfLight,
	createResponsiveStyles,
	createImageBackgroundStyleSet,
	createImageMask,
	__getValue,
} from '~stackable/util'
import { showOptions } from '~stackable/deprecated/v2/block/testimonial/util'
import deepmerge from 'deepmerge'
import { range } from 'lodash'

addFilter( 'stackable.testimonial.styles', 'stackable/premium/testimonial', ( styles, props ) => {
	const getValue = __getValue( props.attributes )

	const newStyles = [ styles ]

	const {
		design = 'basic',
		columns = 3,
		showImage = true,
		testimonialColor = '',
	} = props.attributes

	const show = showOptions( props )

	// Overridden colors.
	range( 1, columns + 1 ).forEach( i => {
		const columnBackgroundColor = props.attributes[ `column${ i }BackgroundColor` ]

		if ( show.columnBackground && columnBackgroundColor ) {
			newStyles.push( {
				[ `.ugb-testimonial__item.ugb-testimonial__item${ i }` ]: {
					backgroundColor: appendImportant( columnBackgroundColor ),
				},
				[ `.ugb-testimonial__item.ugb-testimonial__item${ i }:before` ]: {
					display: 'none',
				},
			} )

			newStyles.push( {
				[ `.ugb-testimonial__item.ugb-testimonial__item${ i } .ugb-testimonial__body, ` +
				  `.ugb-testimonial__item.ugb-testimonial__item${ i } .ugb-testimonial__name, ` +
				  `.ugb-testimonial__item.ugb-testimonial__item${ i } .ugb-testimonial__position` ]: {
					color: appendImportant( isDarkColor( columnBackgroundColor ) ? '#ffffff' : '#222222' ),
				},
			} )
		}
	} )

	// Bubble color.
	if ( design === 'bubble' ) {
		const columnBackgroundColor = getValue( 'bubbleBackgroundColor' )
		if ( columnBackgroundColor ) {
			newStyles.push( {
				[ `.ugb-testimonial__item .ugb-testimonial__body-wrapper, ` +
				  `.ugb-testimonial__item .ugb-testimonial__body-wrapper:after` ]: {
					backgroundColor: appendImportant( columnBackgroundColor ),
				},
			} )

			newStyles.push( {
				[ `.ugb-testimonial__item .ugb-testimonial__body` ]: {
					color: isDarkColor( columnBackgroundColor ) ? '#ffffff' : '#222222',
				},
			} )
		}

		// Overridden colors.
		range( 1, columns + 1 ).forEach( i => {
			const columnBackgroundColor = props.attributes[ `bubble${ i }BackgroundColor` ]

			if ( columnBackgroundColor ) {
				newStyles.push( {
					[ `.ugb-testimonial__item.ugb-testimonial__item${ i } .ugb-testimonial__body-wrapper, ` +
					  `.ugb-testimonial__item.ugb-testimonial__item${ i } .ugb-testimonial__body-wrapper:after` ]: {
						backgroundColor: appendImportant( columnBackgroundColor ),
					},
				} )

				newStyles.push( {
					[ `.ugb-testimonial__item.ugb-testimonial__item${ i } .ugb-testimonial__body` ]: {
						color: isDarkColor( columnBackgroundColor ) ? '#ffffff' : '#222222',
					},
				} )
			}
		} )
	}

	// Bubble design.
	if ( design === 'bubble' ) {
		newStyles.push( {
			// Bubble border radius.
			'.ugb-testimonial__body-wrapper': {
				borderRadius: getValue( 'borderRadius' ) !== 12 ? getValue( 'borderRadius', '%spx !important' ) : undefined,
			},
			'.ugb-testimonial__body-wrapper:after': {
				left: showImage ? getValue( 'imageWidth', `calc(%spx / 2 + 8px)` ) : undefined,
			},
		} )
	}

	// Image as a background position.
	if ( show.imageAsBackground ) {
		newStyles.push( {
			...createImageBackgroundStyleSet( 'image%s', 'ugb-testimonial__image', props.attributes ),
		} )

		// Individual image background position
		range( 1, columns + 1 ).forEach( i => {
			newStyles.push( {
				...createImageBackgroundStyleSet( `image${ i }%s`, `ugb-testimonial__item.ugb-testimonial__item${ i } .ugb-testimonial__image`, props.attributes ),
			} )
		} )
	}

	// Background design, the column background color is the image overlay.
	if ( design === 'background' ) {
		const columnBackgroundColor = getValue( 'columnBackgroundColor' )
		if ( columnBackgroundColor ) {
			const left = hexToRgba( columnBackgroundColor, 0.3 )
			const right = hexToRgba( columnBackgroundColor, 0.8 )

			newStyles.push( {
				'.ugb-testimonial__image:after': {
					background: appendImportant( `linear-gradient(270deg, ${ left }, ${ right })` ),
				},
			} )
		}

		range( 1, columns + 1 ).forEach( i => {
			const columnBackgroundColor = getValue( `column${ i }BackgroundColor` )
			if ( columnBackgroundColor ) {
				const left = hexToRgba( columnBackgroundColor, 0.3 )
				const right = hexToRgba( columnBackgroundColor, 0.8 )

				newStyles.push( {
					[ `.ugb-testimonial__item.ugb-testimonial__item${ i } .ugb-testimonial__image:after` ]: {
						background: appendImportant( `linear-gradient(270deg, ${ left }, ${ right })` ),
					},
				} )
			}
		} )
	}

	if ( design === 'vertical' || design === 'vertical-inverse' ) {
		const columnBackgroundColor = getValue( 'columnBackgroundColor' )
		if ( columnBackgroundColor ) {
			newStyles.push( {
				'.ugb-testimonial__body-wrapper, .ugb-testimonial__person:after': {
					background: appendImportant( columnBackgroundColor ),
				},
				'.ugb-testimonial__body': {
					color: whiteIfDarkBlackIfLight( testimonialColor, columnBackgroundColor ),
				},
			} )
		}

		if ( show.positionSpacing ) {
			newStyles.push( ...createResponsiveStyles( '.ugb-testimonial__person', 'vertical%sImageHeight', 'height', '%spx', props.attributes, { important: true } ) )
		}

		range( 1, columns + 1 ).forEach( i => {
			const columnBackgroundColor = getValue( `column${ i }BackgroundColor` )
			if ( columnBackgroundColor ) {
				newStyles.push( {
					[ `.ugb-testimonial__item.ugb-testimonial__item${ i } .ugb-testimonial__body-wrapper, ` +
					  `.ugb-testimonial__item.ugb-testimonial__item${ i } .ugb-testimonial__person:after` ]: {
						background: appendImportant( columnBackgroundColor ),
					},
					[ `.ugb-testimonial__item.ugb-testimonial__item${ i } .ugb-testimonial__body` ]: {
						color: isDarkColor( columnBackgroundColor ) ? '#ffffff' : '#222222',
					},
				} )
			}
		} )
	}

	// Advanced image.
	if ( ! show.imageAsBackground && showImage ) {
		range( 1, columns + 1 ).forEach( i => {
			if ( props.attributes[ `image${ i }Shape` ] ) {
				newStyles.push( {
					[ `.ugb-testimonial__item${ i } .ugb-img` ]: createImageMask( `image${ i }%s`, props.attributes ),
				} )
			}
		} )
	}

	return newStyles.length === 1 ? newStyles[ 0 ] : deepmerge.all( newStyles )
}, 11 )

// For vertical layouts, add the column advanced paddings inside the content wrapper instead of the box
// since the paddings will affect the image.
addFilter( 'stackable.testimonial.advanced-column-spacing.paddings.selector', 'stackable/testimonial/paddings', ( selector, props ) => {
	const {
		design = 'basic',
	} = props.attributes

	if ( design === 'vertical' || design === 'vertical-inverse' ) {
		return '.ugb-testimonial__body-wrapper'
	}

	return selector
} )
