
/**
 * WordPress dependencies
 */
import { __, sprintf } from '@wordpress/i18n'
import { Fragment } from '@wordpress/element'

/**
 * External dependencies
 */
import { i18n } from 'stackable'
import classnames from 'classnames'
import { AdvancedSelectControl, AdvancedToggleControl } from '~stackable/components'

function EffectsSettings( props ) {
	const {
		hasContainerEffects,
		hoverEffect,
		grayscaleEffect,
		onChangeHoverEffect,
		onChangeGrayscaleEffect,
	} = props

	const hasHelpTip = props.hoverClassName.match( /ugb--help-tip/ )
	const hoverClasses = classnames( [
		props.className,
	], {
		'ugb--help-tip-hover-effect': ! hasHelpTip,
	} )

	return (
		<Fragment>
			{ onChangeHoverEffect &&
				<AdvancedSelectControl
					label={ props.hoverEffectLabel }
					help={ props.help }
					value={ hoverEffect }
					options={ [
						{ value: '', label: __( 'None', i18n ) },
						{ value: 'shadow', label: __( 'Shadow', i18n ) },
						{ value: 'lift', label: __( 'Lift', i18n ) },
						{ value: 'lift-more', label: sprintf( __( '%s More', i18n ), __( 'Lift', i18n ) ) },
						{ value: 'lift-shadow', label: __( 'Lift w/ shadow', i18n ) },
						{ value: 'lift-staggered', label: __( 'Staggered lift', i18n ) },
						{ value: 'lift-shadow-staggered', label: __( 'Staggered lift w/ shadow', i18n ) },
						{ value: 'scale', label: __( 'Scale', i18n ) },
						{ value: 'scale-more', label: sprintf( __( '%s More', i18n ), __( 'Scale', i18n ) ) },
						{ value: 'scale-shadow', label: __( 'Scale w/ shadow', i18n ) },
						{ value: 'lower', label: __( 'Lower', i18n ) },
						{ value: 'lower-more', label: sprintf( __( '%s More', i18n ), __( 'Lower', i18n ) ) },
					]
						.filter( option => {
							if ( ! props.hoverEffectInclude.length ) {
								return true
							}
							return props.hoverEffectInclude.includes( option.value )
						} )
						.filter( option => {
							if ( hasContainerEffects ) {
								return true
							}
							return ! option.value.match( /(shadow|staggered)/ )
						} ) }
					onChange={ onChangeHoverEffect }
					className={ hoverClasses }
				/>
			}
			{ onChangeGrayscaleEffect &&
				<AdvancedToggleControl
					label={ __( 'Grayscale Hover Effect', i18n ) }
					checked={ grayscaleEffect }
					onChange={ onChangeGrayscaleEffect }
				/>
			}
		</Fragment>
	)
}

EffectsSettings.defaultProps = {
	hoverClassName: '',
	hoverEffect: '',
	hoverEffectLabel: __( 'Hover Effect', i18n ),
	onChangeHoverEffect: null,
	hoverEffectInclude: [],
	hasContainerEffects: true,

	grayscaleEffect: '',
	onChangeGrayscaleEffect: null,
}

export default EffectsSettings
