
/**
 * Internal dependencies
 */
import './custom-css'
import './deprecated'
import './style'
import './bubble'
import './background'
import './vertical'
import PanelEffectsSettings from '../../../../components/panel-effects-settings'

/**
 * External dependencies
 */
import {
	i18n, isPro, planName,
} from 'stackable'
import { showOptions } from '~stackable/deprecated/v2/block/testimonial/util'
import {
	ColorPaletteControl,
	PanelAdvancedSettings,
	AdvancedRangeControl,
	ImageBackgroundControlsHelper,
	ImageAltControl,
	ImageShapeControls,
} from '~stackable/components'
import { ResponsiveControl } from '../../../../../../src/deprecated/v2/components'
import { getImageUrlFromCache } from '~stackable/util'
import { range } from 'lodash'

/**
 * WordPress dependencies
 */
import {
	__, _x, sprintf,
} from '@wordpress/i18n'
import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'

if ( isPro && planName !== 'starter' ) {
	addFilter( 'stackable.testimonial.edit.inspector.style.before', 'stackable/testimonial', ( output, props ) => {
		const { setAttributes } = props
		const { hoverEffect = '' } = props.attributes
		return (
			<Fragment>
				{ output }
				<PanelEffectsSettings
					hoverEffect={ hoverEffect }
					onChangeHoverEffect={ hoverEffect => setAttributes( { hoverEffect } ) }
				/>
			</Fragment>
		)
	}, 11 )

	// Per column colors.
	addFilter( 'stackable.testimonial.edit.inspector.advanced.before', 'stackable/testimonial', ( output, props ) => {
		const { setAttributes } = props
		const {
			columns = 3,
			showImage = true,
		} = props.attributes

		const show = showOptions( props )

		return (
			<Fragment>
				{ output }
				{ show.columnBackground && range( 1, columns + 1 ).map( i => (
					<PanelAdvancedSettings
						title={ sprintf( _x( '%s #%d', 'Panel title', i18n ), __( 'Column', i18n ), i ) }
						initialOpen={ false }
						key={ i }
					>
						<p className="components-base-control__help">{ sprintf( __( 'Override settings for column %d', i18n ), i ) }</p>
						{ show.columnBackground &&
							<ColorPaletteControl
								value={ props.attributes[ `column${ i }BackgroundColor` ] || '' }
								onChange={ value => setAttributes( { [ `column${ i }BackgroundColor` ]: value } ) }
								label={ __( 'Column Background', i18n ) }
							/>
						}
					</PanelAdvancedSettings>
				) ) }
				{ showImage && range( 1, columns + 1 ).map( i => {
					return ! show.imageAsBackground && (
						<PanelAdvancedSettings
							title={ sprintf( _x( '%s #%d', 'Panel title', i18n ), __( 'Image', i18n ), i ) }
							initialOpen={ false }
							key={ i }
						>
							<ImageAltControl
								value={ props.attributes[ `image${ i }Alt` ] }
								onChange={ value => setAttributes( { [ `image${ i }Alt` ]: value } ) }
							/>
							<ImageShapeControls
								imageId={ props.attributes[ `image${ i }Id` ] }
								imageSize={ props.attributes[ `image${ i }Size` ] }
								shape={ props.attributes[ `image${ i }Shape` ] || props.attributes.imageShape }
								shapeFlipX={ props.attributes[ `image${ i }ShapeFlipX` ] }
								shapeFlipY={ props.attributes[ `image${ i }ShapeFlipY` ] }
								shapeStretch={ props.attributes[ `image${ i }ShapeStretch` ] }
								onChangeShape={ value => setAttributes( { [ `image${ i }Shape` ]: value } ) }
								onChangeShapeFlipX={ value => setAttributes( { [ `image${ i }ShapeFlipX` ]: value } ) }
								onChangeShapeFlipY={ value => setAttributes( { [ `image${ i }ShapeFlipY` ]: value } ) }
								onChangeShapeStretch={ value => setAttributes( { [ `image${ i }ShapeStretch` ]: value } ) }
							/>
						</PanelAdvancedSettings>
					)
				} ) }
			</Fragment>
		)
	}, 19 )

	// Image as a background settings.
	addFilter( 'stackable.testimonial.edit.inspector.style.testimonial.after', 'stackable/testimonial', ( output, props ) => {
		const { setAttributes } = props
		const {
			design = 'basic',
			showImage = true,
			image1Id = '',
			image2Id = '',
			image3Id = '',
		} = props.attributes

		const show = showOptions( props )

		return (
			<Fragment>
				{ output }
				{ show.imageAsBackground &&
					<PanelAdvancedSettings
						title={ __( 'Image', i18n ) }
						checked={ showImage }
						onChange={ showImage => setAttributes( { showImage } ) }
						toggleOnSetAttributes={ [
							'imageSize',
							'imageBackgroundPosition',
						] }
						toggleAttributeName="showImage"
					>
						{ ( design === 'vertical' || design === 'vertical-inverse' ) &&
							<ResponsiveControl
								attrNameTemplate="vertical%sImageHeight"
								setAttributes={ setAttributes }
								blockAttributes={ props.attributes }
							>
								<AdvancedRangeControl
									label={ __( 'Image Height', i18n ) }
									min={ 0 }
									max={ 400 }
									allowReset={ true }
									placeholder="200"
									className="ugb--help-tip-image-height-crop"
								/>
							</ResponsiveControl>
						}
						<ImageBackgroundControlsHelper
							attrNameTemplate="image%s"
							setAttributes={ setAttributes }
							blockAttributes={ props.attributes }
							onChangeImage={ false }
							onChangeSize={ size => {
								setAttributes( {
									imageSize: size,
									image1Url: getImageUrlFromCache( image1Id, size || 'large' ),
									image2Url: getImageUrlFromCache( image2Id, size || 'large' ),
									image3Url: getImageUrlFromCache( image3Id, size || 'large' ),
								} )
							} }
						/>
					</PanelAdvancedSettings>
				}
			</Fragment>
		)
	} )

	// Per column image as background.
	addFilter( 'stackable.testimonial.edit.inspector.advanced.before', 'stackable/testimonial', ( output, props ) => {
		const { setAttributes } = props
		const {
			columns = 3,
			imageBackgroundPosition = '',
			imageBackgroundRepeat = '',
		} = props.attributes

		const show = showOptions( props )

		return (
			<Fragment>
				{ output }
				{ show.imageAsBackground && range( 1, columns + 1 ).map( i => (
					<PanelAdvancedSettings
						title={ sprintf( _x( '%s #%d', 'Panel title', i18n ), __( 'Image', i18n ), i ) }
						initialOpen={ false }
						key={ i }
					>
						<p className="components-base-control__help">{ sprintf( __( 'Override settings for column %d', i18n ), i ) }</p>
						<ImageBackgroundControlsHelper
							attrNameTemplate={ `image${ i }%s` }
							setAttributes={ setAttributes }
							blockAttributes={ props.attributes }
							onChangeImage={ false }
							onChangeSize={ false }
							backgroundPosition={ props.attributes[ `image${ i }BackgroundPosition` ] || imageBackgroundPosition }
							backgroundRepeat={ props.attributes[ `image${ i }BackgroundRepeat` ] || imageBackgroundRepeat }
						/>
					</PanelAdvancedSettings>
				) ) }
			</Fragment>
		)
	}, 18 )
}

addFilter( 'stackable.testimonial.itemclasses', 'stackable/testimonial', ( classes, props ) => {
	const {
		hoverEffect = '',
	} = props.attributes

	return {
		...classes,
		[ `ugb--hover-${ hoverEffect }` ]: hoverEffect,
	}
} )

addFilter( 'stackable.testimonial.show', 'stackable/testimonial', ( show, blockProps ) => {
	const {
		design = 'basic',
	} = blockProps.attributes
	return {
		...show,
		imageAsBackground: [ 'background', 'vertical', 'vertical-inverse' ].includes( design ) ? true : show.imageAsBackground,
		columnBackground: [ 'bubble' ].includes( design ) ? false : show.columnBackground,
	}
} )

// Image as a background attribute.
addFilter( 'stackable.testimonial.setAttributes', 'stackable/testimonial/bubbleColors', attributes => {
	// When changing the image position, reset the individual image positions.
	if ( typeof attributes.imageBackgroundPosition !== 'undefined' ) {
		return {
			...attributes,
			image1BackgroundPosition: '',
			image2BackgroundPosition: '',
			image3BackgroundPosition: '',
		}
	}
	if ( typeof attributes.imageBackgroundRepeat !== 'undefined' ) {
		return {
			...attributes,
			image1BackgroundRepeat: '',
			image2BackgroundRepeat: '',
			image3BackgroundRepeat: '',
		}
	}
	if ( typeof attributes.imageBackgroundSize !== 'undefined' ) {
		return {
			...attributes,
			image1BackgroundSize: '',
			image2BackgroundSize: '',
			image3BackgroundSize: '',
		}
	}
	if ( typeof attributes.imageBackgroundCustomSize !== 'undefined' || typeof attributes.imageBackgroundCustomSizeUnit !== 'undefined' ) {
		return {
			...attributes,
			image1BackgroundCustomSize: '',
			image2BackgroundCustomSize: '',
			image3BackgroundCustomSize: '',
			image1BackgroundCustomSizeUnit: '',
			image2BackgroundCustomSizeUnit: '',
			image3BackgroundCustomSizeUnit: '',
		}
	}

	return attributes
}, 11 )

// Image attributes.
addFilter( 'stackable.testimonial.setAttributes', 'stackable/testimonial/layoutImageSizes', ( attributes, blockProps ) => {
	const blockAttributes = blockProps.attributes
	let isDefault = false
	if ( blockAttributes.design === 'background' ) {
		if ( blockAttributes.imageSize === 'large' ) {
			isDefault = true
		}
	} else if ( blockAttributes.design === 'vertical' || blockAttributes.design === 'vertical-inverse' ) {
		if ( blockAttributes.imageSize === 'large' ) {
			isDefault = true
		}
	} else if ( blockAttributes.imageSize === 'thumbnail' ) {
		isDefault = true
	}

	// When changing to the Background design, increase the image size
	if ( [ 'vertical', 'vertical-inverse' ].includes( attributes.design ) && isDefault ) {
		return {
			...attributes,
			imageSize: 'large',
			image1Url: getImageUrlFromCache( blockAttributes.image1Id, 'large' ),
			image2Url: getImageUrlFromCache( blockAttributes.image2Id, 'large' ),
			image3Url: getImageUrlFromCache( blockAttributes.image3Id, 'large' ),
		}
	} else if ( attributes.design === 'background' && isDefault ) {
		return {
			...attributes,
			imageSize: 'large',
			image1Url: getImageUrlFromCache( blockAttributes.image1Id, 'large' ),
			image2Url: getImageUrlFromCache( blockAttributes.image2Id, 'large' ),
			image3Url: getImageUrlFromCache( blockAttributes.image3Id, 'large' ),
		}
	} else if ( typeof attributes.design !== 'undefined' && isDefault ) {
		return {
			...attributes,
			imageSize: 'thumbnail',
			image1Url: getImageUrlFromCache( blockAttributes.image1Id, 'thumbnail' ),
			image2Url: getImageUrlFromCache( blockAttributes.image2Id, 'thumbnail' ),
			image3Url: getImageUrlFromCache( blockAttributes.image3Id, 'thumbnail' ),
		}
	}

	return attributes
}, 11 )
