
/**
 * Internal dependencies
 */
import './custom-css'
import './deprecated'
import './style'
import PanelEffectsSettings from '../../../../components/panel-effects-settings'

/**
 * External dependencies
 */
import {
	i18n, isPro,
} from 'stackable'
import { range } from 'lodash'
import {
	ColorPaletteControl,
	ImageAltControl,
	ImageShapeControls,
	PanelAdvancedSettings,
} from '~stackable/components'
import { showOptions } from '~stackable/deprecated/v2/block/feature-grid/util'

/**
 * WordPress dependencies
 */
import {
	__,
	_x,
	sprintf,
} from '@wordpress/i18n'
import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'

/**
 * False if columnNum isn't on the edges of the large-mid layout.
 *
 * @param {string} design The block design
 * @param {number} columnNum Current column index
 * @param {number} columns Total number of columns
 *
 * @return {boolean} False if columnNum isn't on the edges of the large-mid layout.
 */
export const notMidLargeEdgeColumn = ( design, columnNum, columns = 3 ) => {
	if ( design !== 'large-mid' ) {
		return true
	}
	if ( columnNum === 1 ) {
		return false
	}
	if ( columns >= 3 && columnNum === columns ) {
		return false
	}
	return true
}

if ( isPro ) {
	addFilter( 'stackable.feature-grid.edit.inspector.style.before', 'stackable/feature-grid', ( output, props ) => {
		const { setAttributes } = props
		const { hoverEffect = '' } = props.attributes
		return (
			<Fragment>
				{ output }
				<PanelEffectsSettings
					hoverEffect={ hoverEffect }
					onChangeHoverEffect={ hoverEffect => setAttributes( { hoverEffect } ) }
				/>
			</Fragment>
		)
	}, 11 )

	// Per column colors.
	addFilter( 'stackable.feature-grid.edit.inspector.advanced.before', 'stackable/feature-grid', ( output, props ) => {
		const { setAttributes } = props
		const {
			columns = 3,
			showImage = true,
		} = props.attributes

		const show = showOptions( props )

		return (
			<Fragment>
				{ output }
				{ range( 1, columns + 1 ).map( i => (
					show.columnBackground && show[ `columnBackground${ i }` ] && (
						<PanelAdvancedSettings
							title={ sprintf( _x( '%s #%d', 'Panel title', i18n ), __( 'Column', i18n ), i ) }
							initialOpen={ false }
							key={ i }
						>
							<p className="components-base-control__help">{ sprintf( __( 'Override settings for column %d', i18n ), i ) }</p>
							<ColorPaletteControl
								value={ props.attributes[ `column${ i }BackgroundColor` ] || '' }
								onChange={ value => setAttributes( { [ `column${ i }BackgroundColor` ]: value } ) }
								label={ __( 'Column Background', i18n ) }
							/>
						</PanelAdvancedSettings>
					)
				) ) }
				{ showImage && range( 1, columns + 1 ).map( i => {
					return (
						<PanelAdvancedSettings
							title={ sprintf( _x( '%s #%d', 'Panel title', i18n ), __( 'Image', i18n ), i ) }
							initialOpen={ false }
							key={ i }
						>
							<ImageAltControl
								value={ props.attributes[ `image${ i }Alt` ] }
								onChange={ value => setAttributes( { [ `image${ i }Alt` ]: value } ) }
							/>
							<ImageShapeControls
								imageId={ props.attributes[ `image${ i }Id` ] }
								imageSize={ props.attributes[ `image${ i }Size` ] }
								shape={ props.attributes[ `image${ i }Shape` ] || props.attributes.imageShape }
								shapeFlipX={ props.attributes[ `image${ i }ShapeFlipX` ] }
								shapeFlipY={ props.attributes[ `image${ i }ShapeFlipY` ] }
								shapeStretch={ props.attributes[ `image${ i }ShapeStretch` ] }
								onChangeShape={ value => setAttributes( { [ `image${ i }Shape` ]: value } ) }
								onChangeShapeFlipX={ value => setAttributes( { [ `image${ i }ShapeFlipX` ]: value } ) }
								onChangeShapeFlipY={ value => setAttributes( { [ `image${ i }ShapeFlipY` ]: value } ) }
								onChangeShapeStretch={ value => setAttributes( { [ `image${ i }ShapeStretch` ]: value } ) }
							/>
						</PanelAdvancedSettings>
					)
				} ) }
			</Fragment>
		)
	}, 19 )
}

addFilter( 'stackable.feature-grid.itemclasses', 'stackable/feature-grid', ( classes, props, i ) => {
	const {
		hoverEffect = '',
		design = 'basic',
		columns = 3,
		shadow = '',
	} = props.attributes

	const newClasses = {
		...classes,
		[ `ugb--hover-${ hoverEffect }` ]: hoverEffect,
	}

	// For large mid, the first & last won't have shadows.
	if ( design === 'large-mid' ) {
		if ( i === 1 ) {
			newClasses[ `ugb--shadow-${ shadow }` ] = false
		} else if ( columns > 2 && i === columns ) {
			newClasses[ `ugb--shadow-${ shadow }` ] = false
		}
	}

	return newClasses
} )

// Don't apply title colors for the sides for the large-mid design.
addFilter( 'stackable.feature-grid.styles.title.color-selector', 'stackable/feature-grid', ( selector, props ) => {
	const {
		design = 'basic',
		columns = 3,
	} = props.attributes

	if ( design === 'large-mid' ) {
		if ( columns === 4 ) {
			return '.ugb-feature-grid__item2 .ugb-feature-grid__title, .ugb-feature-grid__item3 .ugb-feature-grid__title'
		}
		return '.ugb-feature-grid__item2 .ugb-feature-grid__title'
	}

	return selector
} )

// Don't apply description colors for the sides for the large-mid design.
addFilter( 'stackable.feature-grid.styles.description.color-selector', 'stackable/feature-grid', ( selector, props ) => {
	const {
		design = 'basic',
		columns = 3,
	} = props.attributes

	if ( design === 'large-mid' ) {
		if ( columns === 4 ) {
			return '.ugb-feature-grid__item2 .ugb-feature-grid__description, .ugb-feature-grid__item3 .ugb-feature-grid__description'
		}
		return '.ugb-feature-grid__item2 .ugb-feature-grid__description'
	}

	return selector
} )

// When using large-mid, don't show the backgrounds of some columns.
addFilter( 'stackable.feature-grid.show', 'stackable/feature-grid', ( show, props ) => {
	const {
		design = 'basic',
		columns = 3,
	} = props.attributes

	if ( design === 'large-mid' ) {
		return {
			...show,
			columnBackground1: notMidLargeEdgeColumn( design, 1, columns ),
			columnBackground2: notMidLargeEdgeColumn( design, 2, columns ),
			columnBackground3: notMidLargeEdgeColumn( design, 3, columns ),
			columnBackground4: notMidLargeEdgeColumn( design, 4, columns ),
		}
	}

	return show
} )
