/**
 * WordPress dependencies
 */
import { addFilter } from '@wordpress/hooks'

/**
 * External dependencies
 */
import deepmerge from 'deepmerge'

/**
 * Internal dependencies
 */
import {
	whiteIfDarkBlackIfLight,
	appendImportant,
	__getValue,
} from '~stackable/util'

addFilter( 'stackable.blockquote.styles', 'stackable/premium/blockquote', ( styles, props ) => {
	const getValue = __getValue( props.attributes )

	const newStyles = [ styles ]

	const {
		design = 'basic',
		textColor = '',
	} = props.attributes

	// Border radius on the background image.
	if ( design === 'highlight' ) {
		newStyles.push( {
			'.ugb-blockquote__content': {
				boxShadow: textColor ? `-0.2em 0 0 0.5em ${ textColor }, 0.2em 0 0 0.5em ${ textColor } !important` : undefined,
				backgroundColor: appendImportant( getValue( 'textColor' ) ),
				fontSize: appendImportant( getValue( 'textFontSize', `%s${ getValue( 'textFontSizeUnit' ) }` ) ),
			},
			'.ugb-blockquote__text': {
				backgroundColor: appendImportant( getValue( 'textColor' ) ),
				color: appendImportant( whiteIfDarkBlackIfLight( '', textColor ) ),
			},
			tablet: {
				'.ugb-blockquote__content': {
					fontSize: appendImportant( getValue( 'textTabletFontSize', `%s${ getValue( 'textTabletFontSizeUnit' ) }` ) ),
				},
			},
			mobile: {
				'.ugb-blockquote__content': {
					fontSize: appendImportant( getValue( 'textMobileFontSize', `%s${ getValue( 'textMobileFontSizeUnit' ) }` ) ),
				},
			},
			editor: {
				'.ugb-blockquote': {
					'--ugb-box-color': appendImportant( getValue( 'textColor' ) ),
				},
			},
		} )
	}

	return newStyles.length === 1 ? newStyles[ 0 ] : deepmerge.all( newStyles )
}, 11 )
