
/**
 * WordPress dependencies
 */
import { __, sprintf } from '@wordpress/i18n'
import {
	addAction, addFilter, removeFilter,
} from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'

/**
 * External dependencies
 */
import {
	BlendModeControl,
	ButtonIconPopoverControl,
	ColorPaletteControl,
	AdvancedRangeControl,
	AdvancedToggleControl,
} from '~stackable/components'
import {
	camelCase, capitalize, lowerCase,
} from 'lodash'
import { i18n, isPro } from 'stackable'
import deepmerge from 'deepmerge'
import { __getValue } from '~stackable/util'

/**
 * Internal dependencies
 */
import SeparatorLayer from '../../../../components/separator-layer'

const layerEditControls = ( location = 'Top', layer = 2 ) => ( output, props ) => {
	const getAttrName = attrNameTemplate => camelCase( sprintf( attrNameTemplate, capitalize( location ), layer ) )
	const getValue = __getValue( props.attributes, getAttrName, '' )

	const { setAttributes } = props

	return (
		<Fragment>
			{ output }
			<ButtonIconPopoverControl
				label={ sprintf( __( 'Separator Layer %s', i18n ), layer ) }
				checked={ getValue( 'Show%sSeparatorLayer%s' ) }
				onToggle={ value => setAttributes( { [ getAttrName( 'Show%sSeparatorLayer%s' ) ]: value } ) }
				allowReset={
					getValue( '%sSeparatorLayer%sColor' ) ||
					getValue( '%sSeparatorLayer%sHeight' ) ||
					getValue( '%sSeparatorLayer%sWidth' ) ||
					getValue( '%sSeparatorLayer%sFlipHorizontally' ) ||
					getValue( '%sSeparatorLayer%sOpacity' ) ||
					getValue( '%sSeparatorLayer%sBlendMode' )
				}
				onReset={ () => {
					setAttributes( {
						[ getAttrName( '%sSeparatorLayer%sColor' ) ]: '',
						[ getAttrName( '%sSeparatorLayer%sHeight' ) ]: '',
						[ getAttrName( '%sSeparatorLayer%sWidth' ) ]: '',
						[ getAttrName( '%sSeparatorLayer%sFlipHorizontally' ) ]: '',
						[ getAttrName( '%sSeparatorLayer%sOpacity' ) ]: '',
						[ getAttrName( '%sSeparatorLayer%sBlendMode' ) ]: '',
					} )
				} }
				toggleOnSetAttributes={ [
					getAttrName( '%sSeparatorLayer%sColor' ),
					getAttrName( '%sSeparatorLayer%sHeight' ),
					getAttrName( '%sSeparatorLayer%sWidth' ),
					getAttrName( '%sSeparatorLayer%sFlipHorizontally' ),
					getAttrName( '%sSeparatorLayer%sOpacity' ),
					getAttrName( '%sSeparatorLayer%sBlendMode' ),
				] }
				toggleAttributeName={ getAttrName( 'Show%sSeparatorLayer%s' ) }
				className={ `ugb--help-tip-separator-layer${ layer }` }
			>
				<ColorPaletteControl
					label={ __( 'Color', i18n ) }
					value={ getValue( '%sSeparatorLayer%sColor' ) }
					onChange={ value => setAttributes( { [ getAttrName( '%sSeparatorLayer%sColor' ) ]: value } ) }
				/>
				<AdvancedRangeControl
					label={ __( 'Layer Height', i18n ) }
					value={ getValue( '%sSeparatorLayer%sHeight' ) }
					min="0"
					max="2"
					step="0.01"
					onChange={ value => setAttributes( { [ getAttrName( '%sSeparatorLayer%sHeight' ) ]: value } ) }
					allowReset={ true }
					placeholder="1"
					defaultValue={ 1 }
				/>
				<AdvancedRangeControl
					label={ __( 'Layer Width', i18n ) }
					value={ getValue( '%sSeparatorLayer%sWidth' ) }
					min="1"
					max="4"
					step="0.1"
					onChange={ value => setAttributes( { [ getAttrName( '%sSeparatorLayer%sWidth' ) ]: value } ) }
					allowReset={ true }
					placeholder="1"
					defaultValue={ 1 }
				/>
				<AdvancedToggleControl
					label={ __( 'Flip Horizontally', i18n ) }
					checked={ getValue( '%sSeparatorLayer%sFlipHorizontally' ) }
					onChange={ value => setAttributes( { [ getAttrName( '%sSeparatorLayer%sFlipHorizontally' ) ]: value } ) }
				/>
				<AdvancedRangeControl
					label={ __( 'Opacity', i18n ) }
					value={ getValue( '%sSeparatorLayer%sOpacity' ) }
					min="0"
					max="1"
					step="0.1"
					onChange={ value => setAttributes( { [ getAttrName( '%sSeparatorLayer%sOpacity' ) ]: value } ) }
					allowReset={ true }
					placeholder={ 0.5 }
					className="ugb--help-tip-separator-layer-opacity"
				/>
				{ layer === 2 && (
					<BlendModeControl
						value={ getValue( '%sSeparatorLayer%sBlendMode' ) }
						onChange={ value => setAttributes( { [ getAttrName( '%sSeparatorLayer%sBlendMode' ) ]: value } ) }
						className="ugb--help-tip-separator-layer-blend-mode"
					/>
				) }
			</ButtonIconPopoverControl>
		</Fragment>
	)
}

const addTopSeparatorLayers = ( output, blockProps ) => {
	return (
		<Fragment>
			{ output }
			{ blockProps.attributes.showTopSeparator && blockProps.attributes.showTopSeparatorLayer2 && (
				<SeparatorLayer
					layer="2"
					design={ blockProps.attributes.topSeparatorDesign }
					inverted={ blockProps.attributes.topSeparatorFlipVertically }
				/>
			) }
			{ blockProps.attributes.showTopSeparator && blockProps.attributes.showTopSeparatorLayer3 && (
				<SeparatorLayer
					layer="3"
					design={ blockProps.attributes.topSeparatorDesign }
					inverted={ blockProps.attributes.topSeparatorFlipVertically }
				/>
			) }
		</Fragment>
	)
}

const addBottomSeparatorLayers = ( output, blockProps ) => {
	return (
		<Fragment>
			{ output }
			{ blockProps.attributes.showBottomSeparator && blockProps.attributes.showBottomSeparatorLayer2 && (
				<SeparatorLayer
					layer="2"
					design={ blockProps.attributes.bottomSeparatorDesign }
					inverted={ blockProps.attributes.bottomSeparatorFlipVertically }
				/>
			) }
			{ blockProps.attributes.showBottomSeparator && blockProps.attributes.showBottomSeparatorLayer3 && (
				<SeparatorLayer
					layer="3"
					design={ blockProps.attributes.bottomSeparatorDesign }
					inverted={ blockProps.attributes.bottomSeparatorFlipVertically }
				/>
			) }
		</Fragment>
	)
}

const addLayerStyles = ( location = 'Top', layer = 2 ) => ( styleObject, props ) => {
	const getAttrName = attrNameTemplate => camelCase( sprintf( attrNameTemplate, capitalize( location ), layer ) )
	const getValue = __getValue( props.attributes, getAttrName )

	if ( ! getValue( 'show%sSeparator' ) || ! getValue( 'show%sSeparatorLayer%s' ) ) {
		return styleObject
	}

	let transform = ''
	transform += getValue( '%sSeparatorLayer%sFlipHorizontally' ) ? `scaleX(-1)` : ''
	transform += getValue( '%sSeparatorLayer%sHeight', 'scaleY(%s)' ) || ''
	transform += getValue( '%sSeparatorLayer%sWidth', 'scaleX(%s)' ) || ''

	const styles = {
		[ `.ugb-${ lowerCase( location ) }-separator .ugb-separator__layer-${ layer }` ]: {
			fill: getValue( '%sSeparatorLayer%sColor' ),
			transform: transform !== '' ? transform : undefined,
			opacity: getValue( '%sSeparatorLayer%sOpacity' ),
			mixBlendMode: getValue( '%sSeparatorLayer%sBlendMode' ),
		},
	}

	return deepmerge( styleObject, styles )
}

addAction( `stackable.module.block-separators`, 'stackable.module.block-separators.pro', blockName => {
	if ( isPro ) {
		removeFilter( 'stackable.block-separators.edit.top', 'stackable/module/block-separator/edit/top/layer2' )
		removeFilter( 'stackable.block-separators.edit.top', 'stackable/module/block-separator/edit/top/layer3' )
		removeFilter( 'stackable.block-separators.edit.bottom', 'stackable/module/block-separator/edit/bottom/layer2' )
		removeFilter( 'stackable.block-separators.edit.bottom', 'stackable/module/block-separator/edit/bottom/layer3' )
		addFilter( 'stackable.block-separators.edit.top', 'stackable/module/block-separator/edit/top/layer2', layerEditControls( 'Top', 2 ) )
		addFilter( 'stackable.block-separators.edit.top', 'stackable/module/block-separator/edit/top/layer3', layerEditControls( 'Top', 3 ) )
		addFilter( 'stackable.block-separators.edit.bottom', 'stackable/module/block-separator/edit/bottom/layer2', layerEditControls( 'Bottom', 2 ) )
		addFilter( 'stackable.block-separators.edit.bottom', 'stackable/module/block-separator/edit/bottom/layer3', layerEditControls( 'Bottom', 3 ) )
	}

	removeFilter( `stackable.module.block-separator.output.top.after`, `stackable/module/block-separators/pro/top` )
	removeFilter( `stackable.module.block-separator.output.bottom.after`, `stackable/module/block-separators/pro/bottom` )
	addFilter( `stackable.module.block-separator.output.top.after`, `stackable/module/block-separators/pro/top`, addTopSeparatorLayers )
	addFilter( `stackable.module.block-separator.output.bottom.after`, `stackable/module/block-separators/pro/bottom`, addBottomSeparatorLayers )

	addFilter( `stackable.${ blockName }.styles`, `stackable/${ blockName }/block-separators/pro/top/layer2`, addLayerStyles( 'Top', 2 ) )
	addFilter( `stackable.${ blockName }.styles`, `stackable/${ blockName }/block-separators/pro/top/layer3`, addLayerStyles( 'Top', 3 ) )
	addFilter( `stackable.${ blockName }.styles`, `stackable/${ blockName }/block-separators/pro/bottom/layer2`, addLayerStyles( 'Bottom', 2 ) )
	addFilter( `stackable.${ blockName }.styles`, `stackable/${ blockName }/block-separators/pro/bottom/layer3`, addLayerStyles( 'Bottom', 3 ) )
} )
