/**
 * WordPress dependencies
 */
import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'

/**
 * External dependencies
 */
import { omit } from 'lodash'

addFilter( 'stackable.pricing-box.mainclasses_1_17_3', 'stackable/pricing-box', ( classes, design, props ) => {
	const { customCSSUniqueID = '' } = props.attributes
	return {
		...classes,
		[ customCSSUniqueID ]: customCSSUniqueID,
	}
} )

addFilter( 'stackable.pricing-box.save.output.before_1_17_3', 'stackable/pricing-box', ( output, design, props ) => {
	const { customCSSCompiled = '' } = props.attributes
	if ( ! customCSSCompiled ) {
		return output
	}

	return (
		<Fragment>
			{ output }
			<style>{ customCSSCompiled }</style>
		</Fragment>
	)
} )

addFilter( 'stackable.pricing-box.itemclasses_1_17_3', 'stackable/pricing-box', ( classes, design, i, props ) => {
	const {
		hoverEffect = '',
	} = props.attributes

	return {
		...classes,
		[ `ugb--hover-${ hoverEffect }` ]: hoverEffect,
	}
} )

addFilter( 'stackable.pricing-box.styles_1_17_3', 'stackable/pricing-box', ( styles, design, i, props ) => {
	if ( design === 'sectioned' ) {
		const index = i === 1 ? '' : i
		const highlightColor = props.attributes[ `highlightColor${ index }` ] || ''

		styles.item = omit( styles.item, [ 'backgroundColor' ] )
		styles.title.backgroundColor = highlightColor ? highlightColor : undefined
	} else if ( design === 'colored' ) {
		styles.item = omit( styles.item, [ 'backgroundColor' ] )
	}
	return styles
} )

addFilter( 'stackable.pricing-box.save.output_1_17_3', 'stackable/pricing-box', ( output, design, comps, i, props ) => {
	if ( design === 'compact' ) {
		return (
			<Fragment>
				<div className="ugb-pricing-box__header">
					{ comps.imageComp }
					<div className="ugb-pricing-box__side">
						{ comps.titleComp }
						{ comps.priceComp }
					</div>
				</div>
				{ comps.descriptionComp }
				{ comps.buttonComp }
			</Fragment>
		)
	} else if ( design === 'sectioned' ) {
		return (
			<Fragment>
				{ comps.titleComp }
				{ comps.imageBGComp }
				{ comps.priceComp }
				{ comps.descriptionComp }
				{ comps.buttonComp }
			</Fragment>
		)
	} else if ( design === 'colored' ) {
		const index = i === 1 ? '' : i
		const highlightColor = props.attributes[ `highlightColor${ index }` ] || ''

		const headerStyles = {
			backgroundColor: highlightColor ? highlightColor : undefined,
		}

		return (
			<Fragment>
				<div className="ugb-pricing-box__header" style={ headerStyles }>
					{ comps.imageBGComp }
					{ comps.titleComp }
					{ comps.priceComp }
					{ comps.buttonComp }
				</div>
				{ comps.descriptionComp }
			</Fragment>
		)
	}
	return output
} )
