
/**
 * WordPress dependencies
 */
import { addFilter } from '@wordpress/hooks'

/**
 * External dependencies
 */
import classnames from 'classnames'
import { Fragment } from '@wordpress/element'
import { omit } from 'lodash'

addFilter( 'stackable.count-up.edit.show_1_15_4', 'stackable/count-up', ( show, design ) => {
	if ( design === 'side' || design === 'abstract' ) {
		return {
			...show,
			borderRadius: false,
			shadow: false,
			background: false,
		}
	} else if ( design === 'boxed' ) {
		return {
			...show,
			background: false,
		}
	}
	return show
} )

addFilter( 'stackable.count-up.mainclasses_1_15_4', 'stackable/count-up', ( classes, design, props ) => {
	if ( design === 'boxed' ) {
		const { shadow } = props.attributes
		return omit( classes, `ugb--shadow-${ shadow }` )
	}

	return classes
} )

addFilter( 'stackable.count-up.mainstyle_1_15_4', 'stackable/count-up', ( styles, design ) => {
	if ( design === 'boxed' ) {
		return omit( styles, 'borderRadius' )
	}

	return styles
} )

addFilter( 'stackable.count-up.save.output.before_1_15_4', 'stackable/count-up', ( output, design, props ) => {
	const { customCSSCompiled = '' } = props.attributes
	if ( ! customCSSCompiled ) {
		return output
	}

	return (
		<Fragment>
			{ output }
			<style>{ customCSSCompiled }</style>
		</Fragment>
	)
} )

addFilter( 'stackable.count-up.save.output_1_15_4', 'stackable/count-up', ( output, comps, i, props ) => {
	const {
		design,
		borderRadius,
		shadow,
		backgroundColorType = '',
		backgroundColor,
		backgroundColor2,
		backgroundColorDirection = 0,
	} = props.attributes
	if ( design === 'side' || design === 'abstract' ) {
		return (
			<div className="ugb-countup__item" key={ i }>
				{ comps.countComp }
				<div className="ugb-countup__side">
					{ comps.titleComp }
					{ comps.descriptionComp }
				</div>
			</div>
		)
	}

	if ( design === 'boxed' ) {
		const itemClasses = classnames( [
			'ugb-countup__item',
			`ugb--shadow-${ shadow }`,
		], {
			[ `ugb--has-background-gradient` ]: backgroundColorType === 'gradient',
		} )
		const itemStyles = {
			borderRadius,
			backgroundColor: backgroundColor ? backgroundColor : undefined,
			'--ugb-background-color': backgroundColorType === 'gradient' ? backgroundColor : undefined,
			'--ugb-background-color2': backgroundColorType === 'gradient' && backgroundColor2 ? backgroundColor2 : undefined,
			'--ugb-background-direction': backgroundColorType === 'gradient' ? `${ backgroundColorDirection }deg` : undefined,
		}
		return (
			<div className={ itemClasses } style={ itemStyles } key={ i }>
				{ comps.titleComp }
				{ comps.countComp }
				{ comps.descriptionComp }
			</div>
		)
	}

	return output
} )
