
/**
 * Internal dependencies
 */
import './custom-css'
import './deprecated'
import './style'

/**
 * External dependencies
 */
import {
	i18n, isPro, planName,
} from 'stackable'
import {
	AdvancedRangeControl,
	AdvancedToggleControl,
	WhenResponsiveScreen,
	ImageBackgroundControlsHelper,
	ImageUploadPlaceholder,
	PanelAdvancedSettings,
} from '~stackable/components'
import { __getValue, appendImportant } from '~stackable/util'
import { showOptions } from '~stackable/deprecated/v2/block/container/util'

/**
 * WordPress dependencies
 */
import {
	__,
} from '@wordpress/i18n'
import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'

if ( isPro && planName !== 'starter' ) {
	addFilter( 'stackable.container.edit.inspector.style.column-background.after', 'stackable/container', ( output, props ) => {
		const { setAttributes } = props
		const {
			design = 'basic',

			imageHeight = '',
			imageHeightUnit = 'px',
			imageTabletHeight = '',
			imageTabletHeightUnit = 'px',
			imageMobileHeight = '',
			imageMobileHeightUnit = 'px',

			imageWidth = '',
			imageWidthUnit = '%',
			imageTabletWidth = '',
			imageTabletWidthUnit = '%',
			imageMobileWidth = '',
			imageMobileWidthUnit = '%',

			imageCollapseOnMobile = true,
			imageCollapseOnMobileHeight = 300,
		} = props.attributes

		const show = showOptions( props )

		return (
			<Fragment>
				{ output }
				{ show.image &&
					<PanelAdvancedSettings
						title={ __( 'Image', i18n ) }
						initialOpen={ false }
					>
						<ImageBackgroundControlsHelper
							attrNameTemplate="image%s"
							setAttributes={ setAttributes }
							blockAttributes={ props.attributes }
						/>
						{ design === 'image' &&
							<Fragment>
								<WhenResponsiveScreen>
									<AdvancedRangeControl
										label={ __( 'Image Height', i18n ) }
										units={ [ 'px', 'vh' ] }
										min={ [ 0, 0 ] }
										max={ [ 1000, 100 ] }
										unit={ imageHeightUnit }
										onChangeUnit={ value => setAttributes( { imageHeightUnit: value } ) }
										value={ imageHeight }
										onChange={ value => setAttributes( { imageHeight: value } ) }
										allowReset={ true }
										placeholder={ [ 300, '' ] }
										className="ugb--help-tip-container-image-height"
									/>
								</WhenResponsiveScreen>
								<WhenResponsiveScreen screen="tablet">
									<AdvancedRangeControl
										label={ __( 'Image Height', i18n ) }
										units={ [ 'px', 'vh' ] }
										min={ [ 0, 0 ] }
										max={ [ 1000, 100 ] }
										unit={ imageTabletHeightUnit }
										onChangeUnit={ value => setAttributes( { imageTabletHeightUnit: value } ) }
										value={ imageTabletHeight }
										onChange={ value => setAttributes( { imageTabletHeight: value } ) }
										allowReset={ true }
										className="ugb--help-tip-container-image-height"
									/>
								</WhenResponsiveScreen>
								<WhenResponsiveScreen screen="mobile">
									<AdvancedRangeControl
										label={ __( 'Image Height', i18n ) }
										units={ [ 'px', 'vh' ] }
										min={ [ 0, 0 ] }
										max={ [ 1000, 100 ] }
										unit={ imageMobileHeightUnit }
										onChangeUnit={ value => setAttributes( { imageMobileHeightUnit: value } ) }
										value={ imageMobileHeight }
										onChange={ value => setAttributes( { imageMobileHeight: value } ) }
										allowReset={ true }
										className="ugb--help-tip-container-image-height"
									/>
								</WhenResponsiveScreen>
							</Fragment>
						}
						{ ( design === 'image2' || design === 'image3' ) &&
							<Fragment>
								<WhenResponsiveScreen>
									<AdvancedRangeControl
										label={ __( 'Image Width', i18n ) }
										units={ [ 'px', '%', 'vw' ] }
										min={ [ 0, 0, 0 ] }
										max={ [ 2000, 100, 100 ] }
										unit={ imageWidthUnit }
										onChangeUnit={ value => setAttributes( { imageWidthUnit: value } ) }
										value={ imageWidth }
										onChange={ value => setAttributes( { imageWidth: value } ) }
										allowReset={ true }
										placeholder={ [ '', 50, '' ] }
										className="ugb--help-tip-container-image-width"
									/>
								</WhenResponsiveScreen>
								<WhenResponsiveScreen screen="tablet">
									<AdvancedRangeControl
										label={ __( 'Image Width', i18n ) }
										units={ [ 'px', '%', 'vw' ] }
										min={ [ 0, 0, 0 ] }
										max={ [ 2000, 100, 100 ] }
										unit={ imageTabletWidthUnit }
										onChangeUnit={ value => setAttributes( { imageTabletWidthUnit: value } ) }
										value={ imageTabletWidth }
										onChange={ value => setAttributes( { imageTabletWidth: value } ) }
										allowReset={ true }
										className="ugb--help-tip-container-image-width"
									/>
								</WhenResponsiveScreen>
								<WhenResponsiveScreen screen="mobile">
									<AdvancedRangeControl
										label={ __( 'Image Width', i18n ) }
										units={ [ 'px', '%', 'vw' ] }
										min={ [ 0, 0, 0 ] }
										max={ [ 2000, 100, 100 ] }
										unit={ imageMobileWidthUnit }
										onChangeUnit={ value => setAttributes( { imageMobileWidthUnit: value } ) }
										value={ imageMobileWidth }
										onChange={ value => setAttributes( { imageMobileWidth: value } ) }
										allowReset={ true }
										className="ugb--help-tip-container-image-width"
									/>
								</WhenResponsiveScreen>

								<AdvancedToggleControl
									label={ __( 'Collapse image on Mobile', i18n ) }
									checked={ imageCollapseOnMobile }
									onChange={ imageCollapseOnMobile => setAttributes( { imageCollapseOnMobile } ) }
								/>
								{ imageCollapseOnMobile &&
									<AdvancedRangeControl
										label={ __( 'Collapse image height', i18n ) }
										value={ imageCollapseOnMobileHeight }
										onChange={ imageCollapseOnMobileHeight => setAttributes( { imageCollapseOnMobileHeight } ) }
										min={ 0 }
										max={ 600 }
										allowReset={ true }
										placeholder="300"
									/>
								}
							</Fragment>
						}
					</PanelAdvancedSettings>
				}
			</Fragment>
		)
	} )
}

// Add the image for some layouts.
addFilter( 'stackable.container.edit.wrapper.output', 'stackable/container', ( output, props ) => {
	const { setAttributes } = props
	const {
		design = 'basic',
		imageId = '',
		imageUrl = '',
		imageSize = '',
	} = props.attributes

	return (
		<Fragment>
			{ output }
			{ [ 'image', 'image2', 'image3' ].includes( design ) &&
				<ImageUploadPlaceholder
					imageID={ imageId }
					imageURL={ imageUrl }
					imageSize={ imageSize }
					className="ugb-container__image"
					onRemove={ () => {
						setAttributes( {
							imageUrl: '',
							imageId: '',
						} )
					} }
					onChange={ image => {
						setAttributes( {
							imageUrl: image.url,
							imageId: image.id,
						} )
					} }
				/>
			}
		</Fragment>
	)
} )

// Add the image for some layouts.
addFilter( 'stackable.container.save.wrapper.output', 'stackable/container', ( output, props ) => {
	const {
		design = 'basic',
	} = props.attributes

	return (
		<Fragment>
			{ output }
			{ [ 'image', 'image2', 'image3' ].includes( design ) &&
				<div className="ugb-container__image" />
			}
		</Fragment>
	)
} )

// Collapsed image height on mobile for image2 & image3 layouts.
addFilter( 'stackable.container.mainclasses', 'stackable/container/collapseonmobile', ( classes, props ) => {
	const {
		design = 'basic',
		imageCollapseOnMobile = true,
	} = props.attributes

	return {
		...classes,
		'ugb-container--collapse-on-mobile': ( design === 'image2' || design === 'image3' ) && imageCollapseOnMobile,
	}
} )

// Special rules for: Advanced -> Column spacing -> Content Vertical Alignment
addFilter( 'stackable.container.advanced-column-spacing.vertical-align', 'stackable/container', ( styles, props ) => {
	const getValue = __getValue( props.attributes )

	const {
		design = 'basic',
	} = props.attributes

	if ( design === 'image2' || design === 'image3' ) {
		return {
			'.ugb-container__side': {
				justifyContent: appendImportant( getValue( 'columnContentVerticalAlign' ) ),
			},
			tablet: {
				'.ugb-container__side': {
					justifyContent: appendImportant( getValue( 'tabletColumnContentVerticalAlign' ) ),
				},
			},
			mobile: {
				'.ugb-container__side': {
					justifyContent: appendImportant( getValue( 'mobileColumnContentVerticalAlign' ) ),
				},
			},
		}
	}

	return styles
} )

// Add custom selector for paddings.
addFilter( 'stackable.container.spacing-paddings.selector', 'stackable/container', ( selector, props ) => {
	const {
		design = 'basic',
	} = props.attributes

	if ( [ 'image', 'image2', 'image3' ].includes( design ) ) {
		return '> .ugb-inner-block > .ugb-block-content > .ugb-container__wrapper > .ugb-container__side'
	}

	return selector
} )
