/**
 * WordPress dependencies
 */
import { addFilter } from '@wordpress/hooks'

/**
 * External dependencies
 */
import {
	createResponsiveStyles,
	appendImportant,
	isDarkColor,
	createImageMask,
	__getValue,
} from '~stackable/util'
import deepmerge from 'deepmerge'
import { range } from 'lodash'

/**
 * Internal dependencies
 */
import { showOptions } from '~stackable/deprecated/v2/block/team-member/util'

addFilter( 'stackable.team-member.styles', 'stackable/premium/team-member', ( styles, props ) => {
	const getValue = __getValue( props.attributes ) // eslint-disable-line

	const newStyles = [ styles ]

	const show = showOptions( props )

	const {
		columns = 2,
		design = 'basic',
		showImage = true,
	} = props.attributes

	if ( design === 'overlay' || design === 'overlay-simple' ) {
		newStyles.push( ...createResponsiveStyles( '.ugb-team-member__item', 'image%sBackgroundHeight', 'height', '%spx', props.attributes, { important: true } ) )
	}
	if ( design === 'half' ) {
		newStyles.push( ...createResponsiveStyles( '.ugb-team-member__image', 'image%sBackgroundWidth', 'width', '%s%', props.attributes, { important: true } ) )
	}

	// Overridden colors.
	range( 1, columns + 1 ).forEach( i => {
		const columnBackgroundColor = props.attributes[ `column${ i }BackgroundColor` ]

		if ( show.columnBackground && columnBackgroundColor ) {
			newStyles.push( {
				[ `.ugb-team-member__item.ugb-team-member__item${ i }` ]: {
					backgroundColor: appendImportant( columnBackgroundColor ),
				},
				[ `.ugb-team-member__item.ugb-team-member__item${ i }:before` ]: {
					display: 'none',
				},
			} )

			newStyles.push( {
				[ `.ugb-team-member__item.ugb-team-member__item${ i } .ugb-team-member__name, ` +
				  `.ugb-team-member__item.ugb-team-member__item${ i } .ugb-team-member__position, ` +
				  `.ugb-team-member__item.ugb-team-member__item${ i } .ugb-team-member__description` ]: {
					color: isDarkColor( columnBackgroundColor ) ? '#ffffff' : '#222222',
				},
			} )
		}
	} )

	// Advanced image.
	if ( showImage && ! show.imageAsBackground ) {
		range( 1, columns + 1 ).forEach( i => {
			if ( props.attributes[ `image${ i }Shape` ] ) {
				newStyles.push( {
					[ `.ugb-team-member__item${ i } .ugb-img` ]: createImageMask( `image${ i }%s`, props.attributes ),
				} )
			}
		} )
	}

	return newStyles.length === 1 ? newStyles[ 0 ] : deepmerge.all( newStyles )
}, 11 )
