/**
 * External dependencies
 */
import { isDarkColor } from '~stackable/util'

/**
 * WordPress dependencies
 */
import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'

addFilter( 'stackable.accordion.mainclasses_1_17_3', 'stackable/accordion', ( classes, design, props ) => {
	const { shadow, headingBackgroundColor } = props.attributes
	if ( design === 'line-colored' || design === 'colored' ) {
		return {
			...classes,
			'ugb-accordion--is-dark': headingBackgroundColor ? isDarkColor( headingBackgroundColor ) : false,
			[ `ugb--shadow-${ shadow }` ]: shadow !== 3,
		}
	}
	return classes
} )

addFilter( 'stackable.accordion.mainclasses_1_17_3', 'stackable/accordion', ( classes, design, props ) => {
	const { customCSSUniqueID = '' } = props.attributes
	return {
		...classes,
		[ customCSSUniqueID ]: customCSSUniqueID,
	}
} )

addFilter( 'stackable.accordion.headingclasses_1_17_3', 'stackable/accordion', ( classes, design ) => {
	if ( design === 'line-colored' || design === 'colored' ) {
		return {}
	}
	return classes
} )

addFilter( 'stackable.accordion.styles_1_17_3', 'stackable/accordion', ( styles, design, props ) => {
	const { borderRadius, headingBackgroundColor } = props.attributes

	if ( design === 'line-colored' || design === 'colored' ) {
		return {
			...styles,
			heading: {},
			main: {
				...styles.main,
				backgroundColor: headingBackgroundColor ? headingBackgroundColor : undefined,
				'--ugb-arrow-fill': headingBackgroundColor ? headingBackgroundColor : undefined,
				borderRadius: borderRadius !== 12 ? borderRadius : undefined,
			},
		}
	}
	return styles
} )

addFilter( 'stackable.accordion.save.output.before_1_17_3', 'stackable/accordion', ( output, design, props ) => {
	const { customCSSCompiled = '' } = props.attributes
	if ( ! customCSSCompiled ) {
		return output
	}

	return (
		<Fragment>
			{ output }
			<style>{ customCSSCompiled }</style>
		</Fragment>
	)
} )
