/**
 * WordPress dependencies
 */
import { addFilter } from '@wordpress/hooks'

/**
 * External dependencies
 */
import deepmerge from 'deepmerge'

/**
 * Internal dependencies
 */
import {
	appendImportant,
	createResponsiveStyles,
	createButtonStyleSet,
	__getValue,
	createBorderStyleSet,
} from '~stackable/util'
import {
	createBackgroundStyleSet,
	createBackgroundStyles,
	createBackgroundOverlayStyles,
} from '~stackable/deprecated/v2/util'
import { showOptions } from '~stackable/deprecated/v2/block/blog-posts/util'

addFilter( 'stackable.blog-posts.styles', 'stackable/premium/blog-posts', ( styles, props ) => {
	const getValue = __getValue( props.attributes )

	const newStyles = [ styles ]

	const show = showOptions( props )

	const {
		design = 'basic',
		showImage = true,
	} = props.attributes

	// Border radius on the container.
	if ( show.columnBackground ) {
		newStyles.push( {
			'.ugb-blog-posts__item': {
				borderRadius: appendImportant( getValue( 'borderRadius', '%spx' ) ),
			},
		} )
	}

	if ( show.border ) {
		newStyles.push( {
			...createBorderStyleSet( 'column%s', '.ugb-blog-posts__item', props.attributes ),
		} )
	}

	// Background image tinting.
	if ( design === 'portfolio' || design === 'portfolio2' ) {
		const columnBackgroundOptions = {
			importantBackgroundColor: true,
		}
		newStyles.push( {
			'.ugb-blog-posts__item': {
				...createBackgroundStyles( 'column%s', 'desktop', props.attributes, columnBackgroundOptions ),
				...createBackgroundOverlayStyles( 'column%s', 'desktop', props.attributes, columnBackgroundOptions ),
			},
		} )

		// Disable some styles which are not used in this layout.
		newStyles.push( {
			'.ugb-blog-posts__item': {
				opacity: undefined,
				backgroundSize: undefined,
				backgroundPosition: undefined,
				backgroundRepeat: undefined,
				backgroundBlendMode: undefined,
			},
		} )
	}

	// Image width
	if ( design === 'horizontal-card' ) {
		if ( showImage && show.imageWidth ) {
			newStyles.push( ...createResponsiveStyles( '.ugb-blog-posts__featured-image img', 'image%sWidth', 'width', '%spx', props.attributes, { important: true } ) )
		}
	}

	// Item border radius AND image border radius.
	if ( design === 'vertical-card2' ) {
		newStyles.push( {
			'.ugb-blog-posts__featured-image': {
				borderRadius: appendImportant( getValue( 'borderRadius', '%spx' ) ),
			},
		} )
	}

	// Image height.
	const {
		contentOrder = '',
	} = props.attributes
	if ( design === 'image-card' ) {
		newStyles.push( ...createResponsiveStyles( '.ugb-blog-posts__header', 'image%sHeight', 'minHeight', '%spx', props.attributes, { important: true } ) )

		// Color the header differently.
		const compsToDisplay = ( contentOrder || 'meta,title,excerpt,category' ).split( ',' )
		newStyles.push( {
			[ `.ugb-blog-posts__${ compsToDisplay[ 0 ] }` ]: {
				color: appendImportant( getValue( `${ compsToDisplay[ 0 ] }Color` ) ),
			},
			[ `.ugb-blog-posts__${ compsToDisplay[ 1 ] }` ]: {
				color: appendImportant( getValue( `${ compsToDisplay[ 1 ] }Color` ) ),
			},
		} )
	}

	// Column backgrounds.
	if ( [ 'vertical-card', 'horizontal-card', 'image-card', 'vertical-card2' ].includes( design ) ) {
		const selector = design === 'vertical-card2' ? 'ugb-blog-posts__item' : 'ugb-blog-posts__content'
		const columnBackgroundOptions = {
			importantBackgroundColor: true,
		}
		newStyles.push( {
			...createBackgroundStyleSet( 'column%s', selector, props.attributes, columnBackgroundOptions ),
		} )
	}

	// Load More Button.
	const {
		showLoadMoreButton = false,
	} = props.attributes
	if ( showLoadMoreButton ) {
		newStyles.push( {
			...createButtonStyleSet( 'loadMoreButton%s', 'ugb-blog-posts__load-more-button .ugb-button', props.attributes ),
		} )
		newStyles.push( ...createResponsiveStyles( '.ugb-blog-posts__load-more-button.ugb-button-container', 'loadMoreButton%sAlign', 'textAlign', '%s', props.attributes, { important: true } ) )
	}
	if ( show.loadMoreSpacing ) {
		newStyles.push( ...createResponsiveStyles( '.ugb-blog-posts__load-more-button', 'loadMoreButton%sTopMargin', 'marginTop', '%spx', props.attributes, { important: true } ) )
	}

	// Pagination.
	const {
		showPagination = false,
	} = props.attributes
	if ( showPagination ) {
		newStyles.push( {
			...createButtonStyleSet( 'pagination%s', 'ugb-blog-posts__pagination .ugb-button', props.attributes, { hasActiveStyles: true } ),
		} )
		newStyles.push( ...createResponsiveStyles( '.ugb-blog-posts__pagination.ugb-button-container', 'pagination%sAlign', 'textAlign', '%s', props.attributes, { important: true } ) )
	}

	if ( show.paginationSpacing ) {
		newStyles.push( ...createResponsiveStyles( '.ugb-blog-posts__pagination', 'pagination%sTopMargin', 'marginTop', '%spx', props.attributes, { important: true } ) )
	}

	return newStyles.length === 1 ? newStyles[ 0 ] : deepmerge.all( newStyles )
}, 11 )
