/**
 * WordPress dependencies
 */
import { addFilter } from '@wordpress/hooks'

/**
 * External dependencies
 */
import { createResponsiveStyles } from '~stackable/util'
import deepmerge from 'deepmerge'

addFilter( 'stackable.text.styles', 'stackable/premium/text', ( styles, props ) => {
	const newStyles = [ styles ]

	// Column Gap.
	newStyles.push( ...createResponsiveStyles( '.ugb-text__text-wrapper', '%sColumnGap', 'gridColumnGap', '%spx', props.attributes, { important: true } ) )

	// Content Vertical Align.
	newStyles.push( ...createResponsiveStyles( '.ugb-text__text', '%sColumnContentVerticalAlign', 'justifyContent', '%s', props.attributes, { important: true } ) )

	return newStyles.length === 1 ? newStyles[ 0 ] : deepmerge.all( newStyles )
}, 11 )
