/**
 * WordPress dependencies
 */
import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'

addFilter( 'stackable.image-box.mainclasses_1_17_3', 'stackable/image-box', ( classes, design, props ) => {
	const { customCSSUniqueID = '' } = props.attributes
	return {
		...classes,
		[ customCSSUniqueID ]: customCSSUniqueID,
	}
} )

addFilter( 'stackable.image-box.mainclasses_1_17_3', 'stackable/image-box', ( classes, design, props ) => {
	const { horizontalAlign } = props.attributes
	if ( design === 'line' ) {
		return {
			...classes,
			[ `ugb-image-box--align-${ horizontalAlign }` ]: horizontalAlign,
		}
	}
	return classes
} )

addFilter( 'stackable.image-box.save.output.before_1_17_3', 'stackable/image-box', ( output, design, props ) => {
	const { customCSSCompiled = '' } = props.attributes
	if ( ! customCSSCompiled ) {
		return output
	}

	return (
		<Fragment>
			{ output }
			<style>{ customCSSCompiled }</style>
		</Fragment>
	)
} )

addFilter( 'stackable.image-box.itemclasses_1_17_3', 'stackable/image-box', ( classes, design, i, props ) => {
	const {
		hoverEffect = '',
	} = props.attributes

	return {
		...classes,
		[ `ugb--hover-${ hoverEffect }` ]: hoverEffect,
	}
} )
