/**
 * Contains deprecated code that's preserved for block migration.
 */
/**
 * WordPress dependencies
 */
import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'

/**
 * External dependencies
 */
import { omit } from 'lodash'

addFilter( 'stackable.number-box.mainclasses_1_15', 'stackable/number-box', ( classes, design, props ) => {
	const { customCSSUniqueID = '' } = props.attributes
	return {
		...classes,
		[ customCSSUniqueID ]: customCSSUniqueID,
	}
} )

addFilter( 'stackable.number-box.styles_1_15', 'stackable/number-box', ( styles, design, props ) => {
	const { numberColor } = props.attributes
	if ( [ 'background', 'heading', 'heading2' ].includes( design ) ) {
		styles.number = omit( {
			...styles.number,
			color: numberColor ? numberColor : undefined,
		}, [ 'backgroundColor' ] )
	}
	if ( design === 'faded' ) {
		styles.number = omit( styles.number, [ 'backgroundColor', 'color' ] )
		styles.box = omit( styles.box, [ 'borderRadius', 'backgroundColor' ] )
	}
	return styles
} )

const outputStyle = ( output, design, props ) => {
	const { customCSSCompiled = '' } = props.attributes

	if ( ! customCSSCompiled ) {
		return output
	}

	return (
		<Fragment>
			{ output }
			<style>{ customCSSCompiled }</style>
		</Fragment>
	)
}
addFilter( 'stackable.number-box.save.output.before_1_15', 'stackable/number-box', outputStyle )

addFilter( 'stackable.number-box.boxclasses_1_15', 'stackable/number-box', ( classes, design, props ) => {
	const { shadow } = props.attributes
	if ( design === 'faded' ) {
		return omit( classes, [ `ugb--shadow-${ shadow }` ] )
	}
	return classes
} )
