/**
 * External dependencies
 */
import {
	i18n, isPro, planName,
} from 'stackable'
import {
	ColorPaletteControl,
	PanelAdvancedSettings,
	AdvancedRangeControl,
} from '~stackable/components'
import { range } from 'lodash'

/**
 * WordPress dependencies
 */
import {
	__, _x, sprintf,
} from '@wordpress/i18n'
import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'

if ( isPro && planName !== 'starter' ) {
	// Bubble color.
	addFilter( 'stackable.testimonial.edit.inspector.style.general.after', 'stackable/testimonial', ( output, props ) => {
		const { setAttributes } = props
		const {
			design = 'basic',
			borderRadius = '',
			shadow = '',
		} = props.attributes
		return (
			<Fragment>
				{ output }
				{ design === 'bubble' &&
					<PanelAdvancedSettings
						title={ __( 'Container', i18n ) }
						id="column-background"
						initialOpen={ false }
						className="ugb--help-tip-column-background-on-off"
					>
						<ColorPaletteControl
							value={ props.attributes.bubbleBackgroundColor || '' }
							onChange={ value => setAttributes( { bubbleBackgroundColor: value } ) }
							label={ __( 'Bubble Background', i18n ) }
						/>

						<AdvancedRangeControl
							label={ __( 'Border Radius', i18n ) }
							value={ borderRadius }
							onChange={ borderRadius => setAttributes( { borderRadius } ) }
							min={ 0 }
							max={ 50 }
							allowReset={ true }
							placeholder="12"
							className="ugb--help-tip-general-border-radius"
						/>

						<AdvancedRangeControl
							label={ __( 'Shadow / Outline', i18n ) }
							value={ shadow }
							onChange={ shadow => setAttributes( { shadow } ) }
							min={ 0 }
							max={ 9 }
							allowReset={ true }
							placeholder={ design !== 'basic2' ? 3 : '' }
							className="ugb--help-tip-general-shadow"
						/>
					</PanelAdvancedSettings>
				}
			</Fragment>
		)
	} )

	addFilter( 'stackable.testimonial.edit.inspector.advanced.before', 'stackable/testimonial', ( output, props ) => {
		const { setAttributes } = props
		const {
			design = 'basic',
			columns = 3,
		} = props.attributes

		return (
			<Fragment>
				{ output }
				{ design === 'bubble' && range( 1, columns + 1 ).map( i => (
					<PanelAdvancedSettings
						title={ sprintf( _x( '%s #%d', 'Panel title', i18n ), __( 'Bubble Background', i18n ), i ) }
						initialOpen={ false }
						key={ i }
					>
						<p className="components-base-control__help">{ sprintf( __( 'Override settings for column %d', i18n ), i ) }</p>
						<ColorPaletteControl
							value={ props.attributes[ `bubble${ i }BackgroundColor` ] || '' }
							onChange={ value => setAttributes( { [ `bubble${ i }BackgroundColor` ]: value } ) }
							label={ __( 'Bubble Background', i18n ) }
						/>
					</PanelAdvancedSettings>
				) ) }
			</Fragment>
		)
	}, 19 )
}

addFilter( 'stackable.testimonial.bodywrapperclasses', 'stackable/testimonial/bubbleShadow', ( classes, blockProps ) => {
	const {
		design = 'basic',
		shadow = '',
	} = blockProps.attributes

	if ( design !== 'bubble' ) {
		return classes
	}

	return {
		...classes,
		[ `ugb--shadow-${ shadow }` ]: true,
	}
} )

// Bubble attributes.
addFilter( 'stackable.testimonial.setAttributes', 'stackable/testimonial/bubbleColors', ( attributes, blockProps ) => {
	const blockAttributes = blockProps.attributes

	// When changing to the Bubble design, copy the bubble background color.
	if ( attributes.design === 'bubble' ) {
		return {
			...attributes,
			bubbleBackgroundColor: blockAttributes.columnBackgroundColor,
			bubble1BackgroundColor: blockAttributes.column1BackgroundColor,
			bubble2BackgroundColor: blockAttributes.column2BackgroundColor,
			bubble3BackgroundColor: blockAttributes.column3BackgroundColor,
		}
	}

	// When changing away from a bubble design, copy the background color.
	if ( attributes.design !== 'bubble' && blockAttributes.design === 'bubble' ) {
		return {
			...attributes,
			columnBackgroundColor: blockAttributes.bubbleBackgroundColor,
			column1BackgroundColor: blockAttributes.bubble1BackgroundColor,
			column2BackgroundColor: blockAttributes.bubble2BackgroundColor,
			column3BackgroundColor: blockAttributes.bubble3BackgroundColor,
		}
	}

	// Bubble background color.
	if ( typeof attributes.bubbleBackgroundColor !== 'undefined' ) {
		return {
			...attributes,
			bubble1BackgroundColor: '',
			bubble2BackgroundColor: '',
			bubble3BackgroundColor: '',
		}
	}

	return attributes
}, 11 )
