
/**
 * Internal dependencies
 */
import './custom-css'
import './style'
import './designs'
import './pagination'

/**
 * External dependencies
 */
import { i18n, isPro } from 'stackable'
import {
	AdvancedRangeControl,
	AdvancedTextControl,
	PanelAdvancedSettings,
	ButtonControlsHelper,
	ControlSeparator,
	AlignButtonsControl,
} from '~stackable/components'
import { ResponsiveControl } from '../../../../../../src/deprecated/v2/components'
import {
	createResponsiveAttributeNames,
	createButtonAttributeNames,
} from '~stackable/util'

/**
 * WordPress dependencies
 */
import {
	__,
} from '@wordpress/i18n'
import { TextControl, ToggleControl } from '@wordpress/components'
import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'

if ( isPro ) {
	addFilter( 'stackable.blog-posts.edit.inspector.style.posts.after', 'stackable/blog-posts', ( output, props ) => {
		const { setAttributes } = props
		const {
			postOffset = 0,
			postExclude = '',
			postInclude = '',
		} = props.attributes

		return <Fragment>
			<AdvancedRangeControl
				label={ __( 'Offset', i18n ) }
				min={ 0 }
				max={ 100 }
				allowReset={ true }
				value={ postOffset }
				onChange={ postOffset => setAttributes( { postOffset } ) }
				help={ __( 'Offset your posts by a specific number of items', i18n ) }
				placeholder="0"
			/>
			<AdvancedTextControl
				label={ __( 'Exclude Post IDs', i18n ) }
				type="text"
				value={ postExclude }
				onChange={ postExclude => setAttributes( { postExclude } ) }
				help={ __( 'Excludes specific IDs from the display. Enter post IDs separated by a commas', i18n ) }
			/>
			<AdvancedTextControl
				label={ __( 'Display Specific Post IDs', i18n ) }
				type="text"
				value={ postInclude }
				onChange={ postInclude => setAttributes( { postInclude } ) }
				help={ __( 'Limit display to only these specific IDs. Enter post IDs separated by a commas', i18n ) }
			/>
		</Fragment>
	} )

	addFilter( 'stackable.blog-posts.edit.inspector.style.pagination.after', 'stackable/blog-posts', ( output, props ) => {
		const { setAttributes } = props
		const {
			showPagination = false,
			showNextPrevious = true,
			nextLabel = __( 'Next »', i18n ),
			previousLabel = __( '« Previous', i18n ),
		} = props.attributes

		return <Fragment>
			<PanelAdvancedSettings
				title={ __( 'Pagination', i18n ) }
				id="pagination"
				checked={ showPagination }
				onChange={ showPagination => setAttributes( { showPagination, showLoadMoreButton: false } ) }
				toggleAttributeName="showPagination"
			>
				<ToggleControl
					label={ __( 'Show previous and next buttons', i18n ) }
					checked={ showNextPrevious }
					onChange={ showNextPrevious => setAttributes( { showNextPrevious } ) }
				/>
				{ showNextPrevious && (
					<Fragment>
						<TextControl
							label={ __( 'Previous label', i18n ) }
							value={ previousLabel }
							onChange={ previousLabel => setAttributes( { previousLabel } ) }
						/>
						<TextControl
							label={ __( 'Next label', i18n ) }
							value={ nextLabel }
							onChange={ nextLabel => setAttributes( { nextLabel } ) }
						/>
					</Fragment>
				) }
				<ButtonControlsHelper
					hoverOpacityLabel={ __( 'Hover & Active Opacity', i18n ) }
					hoverColorsLabel={ __( 'Hover & Active Colors', i18n ) }
					attrNameTemplate="pagination%s"
					setAttributes={ setAttributes }
					blockAttributes={ props.attributes }
					onChangeUrl={ false }
					onChangeNewTab={ false }
					onChangeNoFolow={ false }
					enableIcon={ false }
					placehoder="21"
				/>
				<ControlSeparator />
				<ResponsiveControl
					attrNameTemplate="Pagination%sAlign"
					setAttributes={ setAttributes }
					blockAttributes={ props.attributes }
				>
					<AlignButtonsControl
						label={ __( 'Align', i18n ) }
						className="ugb--help-tip-alignment-button"
					/>
				</ResponsiveControl>
			</PanelAdvancedSettings>
		</Fragment>
	} )

	addFilter( 'stackable.blog-posts.edit.inspector.style.read-more.after', 'stackable/blog-posts', ( output, props ) => {
		const { setAttributes } = props
		const {
			showLoadMoreButton = '',
			loadMoreItems = '',
			numberOfItems = '',
		} = props.attributes

		return <Fragment>
			<PanelAdvancedSettings
				title={ __( 'Load More Button', i18n ) }
				id="loadmore"
				checked={ showLoadMoreButton }
				onChange={ showLoadMoreButton => setAttributes( { showLoadMoreButton, showPagination: false } ) }
				toggleOnSetAttributes={ [
					'loadMoreItems',
					...createButtonAttributeNames( 'loadMoreButton%s' ),
					...createResponsiveAttributeNames( 'loadMoreButton%sAlign' ),
				] }
				toggleAttributeName="showLoadMoreButton"
			>
				<AdvancedRangeControl
					label={ __( 'Number of items', i18n ) }
					min={ 1 }
					max={ 100 }
					allowReset={ true }
					value={ loadMoreItems }
					onChange={ loadMoreItems => setAttributes( { loadMoreItems } ) }
					placeholder={ numberOfItems }
				/>
				<ControlSeparator />
				<ButtonControlsHelper
					attrNameTemplate="loadMoreButton%s"
					setAttributes={ setAttributes }
					blockAttributes={ props.attributes }
					onChangeUrl={ false }
					onChangeNewTab={ false }
					onChangeNoFollow={ false }
					placeholder="21"
				/>
				<ControlSeparator />
				<ResponsiveControl
					attrNameTemplate="LoadMoreButton%sAlign"
					setAttributes={ setAttributes }
					blockAttributes={ props.attributes }
				>
					<AlignButtonsControl
						label={ __( 'Align', i18n ) }
						className="ugb--help-tip-alignment-button"
					/>
				</ResponsiveControl>
			</PanelAdvancedSettings>
		</Fragment>
	} )
}

// Include custom post queries for our premium settings.
addFilter( 'stackable.blog-posts.postQuery', 'stackable/blog-posts/premium', ( postQuery, props ) => {
	const {
		postOffset = 0,
		postExclude = '',
		postInclude = '',
		showPagination = false,
	} = props.attributes

	return {
		...postQuery,
		offset: postOffset,
		per_page: showPagination ? -1 : postQuery.per_page || 6, // eslint-disable-line camelcase
		exclude: postExclude.split( ',' ).map( s => parseInt( s, 10 ) ).filter( i => ! isNaN( i ) ),
		include: postInclude.split( ',' ).map( s => parseInt( s, 10 ) ).filter( i => ! isNaN( i ) ),
	}
} )

// Add custom selector for paddings.
addFilter( 'stackable.blog-posts.spacing-paddings.selector', 'stackable/blog-posts', ( selector, props ) => {
	const {
		design = 'basic',
	} = props.attributes

	if ( [ 'vertical-card', 'horizontal-card' ].includes( design ) ) {
		return '.ugb-blog-posts__content'
	}

	if ( design === 'image-card' ) {
		return '.ugb-blog-posts__header, .ugb-blog-posts__content'
	}

	return selector
} )
