/**
 * WordPress dependencies
 */
import { addFilter } from '@wordpress/hooks'

/**
 * External dependencies
 */
import deepmerge from 'deepmerge'
import {
	appendImportant, createResponsiveStyles, __getValue,
} from '~stackable/util'

/**
 * Internal dependencies
 */
import { showOptions } from '~stackable/deprecated/v2/block/image-box/util'

addFilter( 'stackable.image-box.styles', 'stackable/premium/image-box', ( styles, props ) => {
	const getValue = __getValue( props.attributes )

	const newStyles = [ styles ]

	const {
		design = 'basic',
	} = props.attributes

	const show = showOptions( props )

	// Line.
	if ( show.line && design === 'box' ) {
		newStyles.push( {
			'.ugb-image-box__item::after': {
				borderColor: appendImportant( getValue( 'lineColor' ) ),
				borderWidth: appendImportant( getValue( 'lineSize', '%spx' ) ),
			},
		} )
	}
	if ( show.line && design === 'line' ) {
		newStyles.push( {
			'.ugb-image-box__header::after': {
				background: appendImportant( getValue( 'lineColor' ) ),
				height: appendImportant( getValue( 'lineSize', '%spx' ) ),
			},
		} )
	}

	if ( show.lineSpacing ) {
		newStyles.push( ...createResponsiveStyles( '.ugb-image-box__header', 'line%sBottomMargin', 'marginBottom', '%spx', props.attributes, { important: true } ) )
	}

	return newStyles.length === 1 ? newStyles[ 0 ] : deepmerge.all( newStyles )
}, 11 )
