
/**
 * Internal dependencies
 */
import './deprecated'
import './custom-css'
import SeparatorLayer from '../../../../components/separator-layer'

/**
 * External dependencies
 */
import {
	AdvancedToggleControl,
	BlendModeControl,
	ColorPaletteControl,
	PanelAdvancedSettings,
	AdvancedRangeControl,
} from '~stackable/components'
import { i18n, isPro } from 'stackable'
import deepmerge from 'deepmerge'

/**
 * WordPress dependencies
 */
import {
	__, _x, sprintf,
} from '@wordpress/i18n'
import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'

if ( isPro ) {
	addFilter( 'stackable.separator.edit.inspector.style.after', 'stackable/separator', ( output, props ) => {
		const { attributes, setAttributes } = props
		const {
			layer2 = false,
			layer2Color = '',
			layer2Height = 1,
			layer2Width = 1,
			layer2Flip = false,
			layer2Opacity = 0.5,
			layer2BlendMode = '',
			layer3 = false,
			layer3Color = '',
			layer3Height = 1,
			layer3Width = 1,
			layer3Flip = false,
			layer3Opacity = 0.5,
		} = attributes

		return (
			<Fragment>
				<PanelAdvancedSettings
					title={ sprintf( _x( '%s %d', 'Nth Title', i18n ), __( 'Layer', i18n ), 2 ) }
					checked={ layer2 }
					onChange={ layer2 => setAttributes( { layer2 } ) }
					toggleOnSetAttributes={ [
						'layer2Color',
						'layer2Height',
						'layer2Width',
						'layer2Flip',
						'layer2Opacity',
						'layer2BlendMode',
					] }
					toggleAttributeName="layer2"
					className="ugb--help-tip-separator-layer2"
				>
					<ColorPaletteControl
						label={ __( 'Layer Color', i18n ) }
						value={ layer2Color }
						onChange={ layer2Color => setAttributes( { layer2Color } ) }
					/>
					<AdvancedRangeControl
						label={ __( 'Layer Height', i18n ) }
						value={ layer2Height }
						min="0"
						max="2"
						step="0.01"
						onChange={ layer2Height => setAttributes( { layer2Height } ) }
						allowReset={ true }
						defaultValue={ 1 }
					/>
					<AdvancedRangeControl
						label={ __( 'Layer Width', i18n ) }
						value={ layer2Width }
						min="1"
						max="4"
						step="0.1"
						onChange={ layer2Width => setAttributes( { layer2Width } ) }
						allowReset={ true }
						defaultValue={ 1 }
					/>
					<AdvancedToggleControl
						label={ __( 'Flip Horizontally', i18n ) }
						checked={ layer2Flip }
						onChange={ layer2Flip => setAttributes( { layer2Flip } ) }
					/>
					<AdvancedRangeControl
						label={ __( 'Opacity', i18n ) }
						value={ layer2Opacity }
						min="0"
						max="1"
						step="0.1"
						onChange={ layer2Opacity => setAttributes( { layer2Opacity } ) }
						allowReset={ true }
						defaultValue={ 0.5 }
						className="ugb--help-tip-separator-layer-opacity"
					/>
					<BlendModeControl
						value={ layer2BlendMode }
						onChange={ layer2BlendMode => setAttributes( { layer2BlendMode } ) }
						className="ugb--help-tip-separator-layer-blend-mode"
					/>
				</PanelAdvancedSettings>
				<PanelAdvancedSettings
					title={ sprintf( _x( '%s %d', 'Nth Title', i18n ), __( 'Layer', i18n ), 3 ) }
					checked={ layer3 }
					onChange={ layer3 => setAttributes( { layer3 } ) }
					toggleOnSetAttributes={ [
						'layer3Color',
						'layer3Height',
						'layer3Width',
						'layer3Flip',
						'layer3Opacity',
						'layer3BlendMode',
					] }
					toggleAttributeName="layer3"
					className="ugb--help-tip-separator-layer3"
				>
					<ColorPaletteControl
						label={ __( 'Layer Color', i18n ) }
						value={ layer3Color }
						onChange={ layer3Color => setAttributes( { layer3Color } ) }
					/>
					<AdvancedRangeControl
						label={ __( 'Layer Height', i18n ) }
						value={ layer3Height }
						min="0"
						max="2"
						step="0.01"
						onChange={ layer3Height => setAttributes( { layer3Height } ) }
						allowReset={ true }
						defaultValue={ 1 }
					/>
					<AdvancedRangeControl
						label={ __( 'Layer Width', i18n ) }
						value={ layer3Width }
						min="1"
						max="4"
						step="0.1"
						onChange={ layer3Width => setAttributes( { layer3Width } ) }
						allowReset={ true }
						defaultValue={ 1 }
					/>
					<AdvancedToggleControl
						label={ __( 'Flip Horizontally', i18n ) }
						checked={ layer3Flip }
						onChange={ layer3Flip => setAttributes( { layer3Flip } ) }
					/>
					<AdvancedRangeControl
						label={ __( 'Opacity', i18n ) }
						value={ layer3Opacity }
						min="0"
						max="1"
						step="0.1"
						onChange={ layer3Opacity => setAttributes( { layer3Opacity } ) }
						allowReset={ true }
						defaultValue={ 0.5 }
						className="ugb--help-tip-separator-layer-opacity"
					/>
				</PanelAdvancedSettings>
			</Fragment>
		)
	} )
}

const layer2Output = ( output, design, props ) => {
	const {
		layer2 = false,
	} = props.attributes

	if ( layer2 ) {
		return (
			<Fragment>
				{ output }
				<SeparatorLayer
					layer="2"
					design={ design }
				/>
			</Fragment>
		)
	}
	return output
}

addFilter( 'stackable.separator.edit.output.layers', 'stackable/separator/layer2', layer2Output, 7 )
addFilter( 'stackable.separator.save.output.layers', 'stackable/separator/layer2', layer2Output, 7 )

const layer3Output = ( output, design, props ) => {
	const {
		layer3 = false,
	} = props.attributes

	if ( layer3 ) {
		return (
			<Fragment>
				{ output }
				<SeparatorLayer
					layer="3"
					design={ design }
				/>
			</Fragment>
		)
	}
	return output
}

addFilter( 'stackable.separator.edit.output.layers', 'stackable/separator/layer3', layer3Output, 8 )
addFilter( 'stackable.separator.save.output.layers', 'stackable/separator/layer3', layer3Output, 8 )

const layer2Styles = ( styleObject, props ) => {
	const {
		layer2 = false,
		layer2Color = '',
		layer2Height = 1,
		layer2Width = 1,
		layer2Flip = false,
		layer2Opacity = 0.5,
		layer2BlendMode = '',
	} = props.attributes

	if ( ! layer2 ) {
		return styleObject
	}

	let layer2Transform = ''
	layer2Transform += layer2Width ? ` scaleX(${ layer2Width })` : ''
	layer2Transform += layer2Flip ? ` scaleX(-1)` : ''
	layer2Transform += layer2Height ? ` scaleY(${ layer2Height })` : ''

	return deepmerge( styleObject, {
		'.ugb-separator__layer-2': {
			transform: layer2Transform ? layer2Transform : undefined,
			fill: layer2Color ? layer2Color : undefined,
			opacity: layer2Opacity,
			mixBlendMode: layer2BlendMode ? layer2BlendMode : undefined,
		},
	} )
}

addFilter( 'stackable.separator.styles', 'stackable/separator/layer2', layer2Styles )

const layer3Styles = ( styleObject, props ) => {
	const {
		layer3 = false,
		layer3Color = '',
		layer3Height = 1,
		layer3Width = 1,
		layer3Flip = false,
		layer3Opacity = 0.5,
		layer3BlendMode = '',
	} = props.attributes

	if ( ! layer3 ) {
		return styleObject
	}

	let layer3Transform = ''
	layer3Transform += layer3Width ? ` scaleX(${ layer3Width })` : ''
	layer3Transform += layer3Flip ? ` scaleX(-1)` : ''
	layer3Transform += layer3Height ? ` scaleY(${ layer3Height })` : ''

	return deepmerge( styleObject, {
		'.ugb-separator__layer-3': {
			transform: layer3Transform ? layer3Transform : undefined,
			fill: layer3Color ? layer3Color : undefined,
			opacity: layer3Opacity,
			mixBlendMode: layer3BlendMode ? layer3BlendMode : undefined,
		},
	} )
}

addFilter( 'stackable.separator.styles', 'stackable/separator/layer3', layer3Styles )
