/**
 * WordPress dependencies
 */
import { addFilter } from '@wordpress/hooks'

/**
 * External dependencies
 */
import {
	appendImportant,
	isDarkColor,
	createImageMask,
	__getValue,
} from '~stackable/util'
import deepmerge from 'deepmerge'
import { range } from 'lodash'

/**
 * Internal dependencies
 */
import { showOptions } from '~stackable/deprecated/v2/block/pricing-box/util'

addFilter( 'stackable.pricing-box.styles', 'stackable/premium/pricing-box', ( styles, props ) => {
	const getValue = __getValue( props.attributes )

	const newStyles = [ styles ]

	const {
		columns = 3,
		design = 'basic',
		showImage = true,
		titleColor = '',
		priceColor = '',
		pricePrefixColor = '',
		priceSuffixColor = '',
		subPriceColor = '',
	} = props.attributes

	const show = showOptions( props )

	// Overridden colors.
	range( 1, columns + 1 ).forEach( i => {
		const columnBackgroundColor = props.attributes[ `column${ i }BackgroundColor` ]

		if ( show.columnBackground && columnBackgroundColor ) {
			newStyles.push( {
				[ `.ugb-pricing-box__item.ugb-pricing-box__item${ i }` ]: {
					backgroundColor: appendImportant( columnBackgroundColor ),
				},
				[ `.ugb-pricing-box__item.ugb-pricing-box__item${ i }:before` ]: {
					display: 'none',
				},
			} )

			newStyles.push( {
				[ `.ugb-pricing-box__item.ugb-pricing-box__item${ i } .ugb-pricing-box__title` ]: {
					color: appendImportant( titleColor ? titleColor : isDarkColor( columnBackgroundColor ) ? '#ffffff' : '#222222' ),
				},
				[ `.ugb-pricing-box__item.ugb-pricing-box__item${ i } .ugb-pricing-box__price` ]: {
					color: appendImportant( priceColor ? priceColor : isDarkColor( columnBackgroundColor ) ? '#ffffff' : '#222222' ),
				},
				[ `.ugb-pricing-box__item.ugb-pricing-box__item${ i } .ugb-pricing-box__price-prefix` ]: {
					color: appendImportant( pricePrefixColor ? pricePrefixColor : isDarkColor( columnBackgroundColor ) ? '#ffffff' : '#222222' ),
				},
				[ `.ugb-pricing-box__item.ugb-pricing-box__item${ i } .ugb-pricing-box__price-suffix` ]: {
					color: appendImportant( priceSuffixColor ? priceSuffixColor : isDarkColor( columnBackgroundColor ) ? '#ffffff' : '#222222' ),
				},
				[ `.ugb-pricing-box__item.ugb-pricing-box__item${ i } .ugb-pricing-box__subprice` ]: {
					color: appendImportant( subPriceColor ? subPriceColor : isDarkColor( columnBackgroundColor ) ? '#ffffff' : '#222222' ),
				},
			} )
		}
	} )

	if ( design === 'colored' ) {
		range( 1, columns + 1 ).forEach( i => {
			const columnBackgroundColor = props.attributes[ `column${ i }HeaderBackgroundColor` ] || '#222'

			if ( columnBackgroundColor ) {
				newStyles.push( {
					[ `.ugb-pricing-box__item.ugb-pricing-box__item${ i } .ugb-pricing-box__header` ]: {
						backgroundColor: appendImportant( columnBackgroundColor ),
					},
				} )

				newStyles.push( {
					[ `.ugb-pricing-box__item.ugb-pricing-box__item${ i } .ugb-pricing-box__title` ]: {
						color: appendImportant( titleColor ? titleColor : isDarkColor( columnBackgroundColor ) ? '#ffffff' : '#222222' ),
					},
					[ `.ugb-pricing-box__item.ugb-pricing-box__item${ i } .ugb-pricing-box__price` ]: {
						color: appendImportant( priceColor ? priceColor : isDarkColor( columnBackgroundColor ) ? '#ffffff' : '#222222' ),
					},
					[ `.ugb-pricing-box__item.ugb-pricing-box__item${ i } .ugb-pricing-box__price-prefix` ]: {
						color: appendImportant( pricePrefixColor ? pricePrefixColor : isDarkColor( columnBackgroundColor ) ? '#ffffff' : '#222222' ),
					},
					[ `.ugb-pricing-box__item.ugb-pricing-box__item${ i } .ugb-pricing-box__price-suffix` ]: {
						color: appendImportant( priceSuffixColor ? priceSuffixColor : isDarkColor( columnBackgroundColor ) ? '#ffffff' : '#222222' ),
					},
					[ `.ugb-pricing-box__item.ugb-pricing-box__item${ i } .ugb-pricing-box__subprice` ]: {
						color: appendImportant( subPriceColor ? subPriceColor : isDarkColor( columnBackgroundColor ) ? '#ffffff' : '#222222' ),
					},
				} )
			}
		} )
	}

	if ( design === 'sectioned' ) {
		// Title Background Color.
		range( 1, columns + 1 ).forEach( i => {
			const columnBackgroundColor = props.attributes[ `column${ i }HeaderBackgroundColor` ] || '#222'

			if ( columnBackgroundColor ) {
				newStyles.push( {
					[ `.ugb-pricing-box__item.ugb-pricing-box__item${ i } .ugb-pricing-box__title` ]: {
						backgroundColor: appendImportant( columnBackgroundColor ),
						color: appendImportant( titleColor ? titleColor : isDarkColor( columnBackgroundColor ) ? '#ffffff' : '#222222' ),
					},
				} )
			}
		} )

		// Background Image.
		range( 1, columns + 1 ).forEach( i => {
			newStyles.push( {
				[ `.ugb-pricing-box__item.ugb-pricing-box__item${ i } .ugb-pricing-box__image` ]: {
					backgroundImage: getValue( `image${ i }Url`, 'url(%s)' ),
				},
			} )
		} )

		// Background Image height.
		newStyles.push( {
			'.ugb-pricing-box__image': {
				height: appendImportant( getValue( 'imageHeight', '%spx' ) ),
			},
			tablet: {
				'.ugb-pricing-box__image': {
					height: appendImportant( getValue( 'imageTabletHeight', '%spx' ) ),
				},
			},
			mobile: {
				'.ugb-pricing-box__image': {
					height: appendImportant( getValue( 'imageMobileHeight', '%spx' ) ),
				},
			},
		} )
	}

	// Advanced image.
	if ( showImage ) {
		range( 1, columns + 1 ).forEach( i => {
			if ( props.attributes[ `image${ i }Shape` ] ) {
				newStyles.push( {
					[ `.ugb-pricing-box__item${ i } .ugb-img` ]: createImageMask( `image${ i }%s`, props.attributes ),
				} )
			}
		} )
	}

	return newStyles.length === 1 ? newStyles[ 0 ] : deepmerge.all( newStyles )
}, 11 )
