/**
 * WordPress dependencies
 */
import { addFilter } from '@wordpress/hooks'

/**
 * External dependencies
 */
import deepmerge from 'deepmerge'
import { __getValue } from '~stackable/util'

addFilter( 'stackable.notification.styles', 'stackable/premium/notification', ( styles, props ) => {
	const getValue = __getValue( props.attributes )

	const newStyles = [ styles ]

	const {
		design = 'basic',
	} = props.attributes

	if ( design === 'bordered' || design === 'outlined' ) {
		newStyles.push( {
			'.ugb-notification__item': {
				borderWidth: getValue( 'columnBorderThickness', '%spx !important' ),
				borderColor: getValue( 'columnBorderColor', '%s !important' ),
			},
		} )
	}

	if ( design === 'large-icon' ) {
		newStyles.push( {
			'.ugb-notification__icon': {
				top: getValue( 'iconTop', '%spx !important' ),
				left: getValue( 'iconLeft', '%spx !important' ),
			},
		} )
	}

	return newStyles.length === 1 ? newStyles[ 0 ] : deepmerge.all( newStyles )
}, 11 )
