/**
 * External dependencies
 */
import {
	i18n, isPro, planName,
} from 'stackable'
import {
	ColorPaletteControl,
	PanelAdvancedSettings,
	AdvancedRangeControl,
	BorderControlsHelper,
} from '~stackable/components'
import { range } from 'lodash'

/**
 * WordPress dependencies
 */
import {
	__, _x, sprintf,
} from '@wordpress/i18n'
import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'

if ( isPro && planName !== 'starter' ) {
	// Column background color.
	addFilter( 'stackable.testimonial.edit.inspector.style.container.before', 'stackable/testimonial', ( output, props ) => {
		const { setAttributes } = props
		const {
			design = 'basic',
			borderRadius = '',
			shadow = '',
		} = props.attributes

		return (
			<Fragment>
				{ design !== 'background' && output }
				{ design === 'background' && (
					<Fragment>
						<ColorPaletteControl
							value={ props.attributes.columnBackgroundColor || '' }
							onChange={ value => setAttributes( { columnBackgroundColor: value } ) }
							label={ __( 'Column Background', i18n ) }
						/>

						<BorderControlsHelper
							attrNameTemplate="column%s"
							setAttributes={ setAttributes }
							blockAttributes={ props.attributes }
						/>

						<AdvancedRangeControl
							label={ __( 'Border Radius', i18n ) }
							value={ borderRadius }
							onChange={ borderRadius => setAttributes( { borderRadius } ) }
							min={ 0 }
							max={ 50 }
							allowReset={ true }
							placeholder="12"
							className="ugb--help-tip-general-border-radius"
						/>

						<AdvancedRangeControl
							label={ __( 'Shadow / Outline', i18n ) }
							value={ shadow }
							onChange={ shadow => setAttributes( { shadow } ) }
							min={ 0 }
							max={ 9 }
							allowReset={ true }
							placeholder={ design !== 'basic2' ? 3 : '' }
							className="ugb--help-tip-general-shadow"
						/>
					</Fragment>
				) }
			</Fragment>
		)
	} )

	addFilter( 'stackable.testimonial.edit.inspector.advanced.before', 'stackable/testimonial', ( output, props ) => {
		const { setAttributes } = props
		const {
			design = 'basic',
			columns = 3,
		} = props.attributes

		return (
			<Fragment>
				{ output }
				{ design === 'background' && range( 1, columns + 1 ).map( i => (
					<PanelAdvancedSettings
						title={ sprintf( _x( '%s #%d', 'Panel title', i18n ), __( 'Column Background', i18n ), i ) }
						initialOpen={ false }
						key={ i }
					>
						<p className="components-base-control__help">{ sprintf( __( 'Override settings for column %d', i18n ), i ) }</p>
						<ColorPaletteControl
							value={ props.attributes[ `column${ i }BackgroundColor` ] || '' }
							onChange={ value => setAttributes( { [ `column${ i }BackgroundColor` ]: value } ) }
							label={ __( 'Column Background', i18n ) }
						/>
					</PanelAdvancedSettings>
				) ) }
			</Fragment>
		)
	}, 19 )
}
