/**
 * WordPress dependencies
 */
import { addFilter } from '@wordpress/hooks'

/**
 * External dependencies
 */
import deepmerge from 'deepmerge'
import {
	appendImportant,
	__getValue,
	createResponsiveStyles,
	createResponsiveEditorStyles,
} from '~stackable/util'
import { showOptions } from '~stackable/deprecated/v2/block/columns/util'

addFilter( 'stackable.columns.styles', 'stackable/premium/columns', ( styles, props ) => {
	const getValue = __getValue( props.attributes )

	const newStyles = [ styles ]

	const show = showOptions( props )

	const {
		collapsedColumnsOrder = '',
		uniqueClass = '',
	} = props.attributes

	// Collapsed row gap.
	newStyles.push( {
		mobile: {
			[ `.${ uniqueClass }-content-wrapper` ]: {
				gridRowGap: appendImportant( getValue( 'collapsedRowGap' ) ),
			},
		},
	} )

	// Collapsed column order.
	if ( collapsedColumnsOrder ) {
		collapsedColumnsOrder.split( ',' ).forEach( ( nthColumn, i ) => {
			newStyles.push( {
				mobile: {
					[ `> .ugb-inner-block > .ugb-block-content > .ugb-columns__item > .ugb-column:nth-child(${ nthColumn })` ]: {
						gridArea: appendImportant( i + 1 ),
					},
				},
				editor: {
					mobile: {
						[ `> .ugb-inner-block > .ugb-block-content > .ugb-columns__item > .block-editor-inner-blocks > .block-editor-block-list__layout > .block-editor-block-list__block:nth-child(${ nthColumn })` ]: {
							gridArea: appendImportant( i + 1 ),
						},
					},
				},
			} )
		} )
	}

	// Column gap.
	newStyles.push( ...createResponsiveStyles( `.${ uniqueClass }-content-wrapper`, '%sColumnGap', 'gridColumnGap', '%spx', props.attributes, { important: true } ) )
	newStyles.push( ...createResponsiveEditorStyles( `.${ uniqueClass }-content-wrapper > .block-editor-inner-blocks > .block-editor-block-list__layout`, '%sColumnGap', 'gridColumnGap', '%spx', props.attributes, { important: true } ) )

	// Row gap.
	if ( show.rowGap ) {
		newStyles.push( ...createResponsiveStyles( `.${ uniqueClass }-content-wrapper`, '%sRowGap', 'gridRowGap', '%spx', props.attributes, { important: true } ) )
		newStyles.push( ...createResponsiveEditorStyles( `.${ uniqueClass }-content-wrapper > .block-editor-inner-blocks > .block-editor-block-list__layout`, '%sRowGap', 'gridRowGap', '%spx', props.attributes, { important: true } ) )
	}

	return newStyles.length === 1 ? newStyles[ 0 ] : deepmerge.all( newStyles )
}, 11 )
