
/**
 * WordPress dependencies
 */
import {
	BaseControl,
} from '@wordpress/components'
import {
	Component,
} from '@wordpress/element'

/**
 * External dependencies
 */
import { compileCSS, minifyCSS } from '~stackable/util'
import { __ } from '@wordpress/i18n'
import { CodeTextarea, PanelAdvancedSettings } from '~stackable/components'
import { i18n } from 'stackable'
import { useDeviceType } from '~stackable/hooks'

class PanelCustomCSSSettings extends Component {
	constructor() {
		super( ...arguments )
		this.onChange = this.onChange.bind( this )
		this.minifiedDefaultCSS = minifyCSS( this.props.defaultCSS )
	}

	shouldComponentUpdate( nextProps ) {
		return this.props.value !== nextProps.value || this.props.defaultCSS !== nextProps.defaultCSS
	}

	onChange( customCSS ) {
		this.props.onChange( this.minifiedDefaultCSS !== minifyCSS( customCSS ) ? customCSS : '' )
	}

	render() {
		const value = this.props.value || this.props.defaultCSS
		return (
			<PanelAdvancedSettings
				className="ugb-custom-css-panel"
				initialOpen={ false }
				title={ __( 'Custom CSS', i18n ) }
			>
				<p className="ugb-custom-css-description">{ __( 'You can use this area to further customize your block. Any custom CSS added here will only affect this block.', i18n ) }</p>
				<BaseControl className="ugb-custom-css-control">
					<CodeTextarea value={ value } onChange={ this.onChange } />
				</BaseControl>
				{ this.props.children }
			</PanelAdvancedSettings>
		)
	}
}

PanelCustomCSSSettings.defaultProps = {
	value: '',
	defaultCSS: '',
	onChange: () => {},
}

PanelCustomCSSSettings.Content = props => {
	const {
		defaultCSS,
		mainClass,
		value,
		uniqueClass,
		isEditor = false,
		deviceType,
	} = props

	// Parse the CSS.
	const newMinified = minifyCSS( compileCSS( value, mainClass, uniqueClass, isEditor, deviceType ), true )
	const defaultMinified = minifyCSS( compileCSS( defaultCSS, mainClass, uniqueClass, isEditor, deviceType ), true )

	return defaultMinified !== newMinified ? <style>{ newMinified }</style> : null
}

PanelCustomCSSSettings.Content.defaultProps = {
	value: '',
	defaultCSS: '',
	mainClass: '',
	uniqueClass: '',
	isEditor: false,
	devieType: 'Desktop',
}

PanelCustomCSSSettings.Edit = props => {
	const deviceType = useDeviceType() // eslint-disable-line
	return <PanelCustomCSSSettings.Content { ...props } deviceType={ deviceType } isEditor={ true } />
}

PanelCustomCSSSettings.Edit.defaultProps = {
	value: '',
	defaultCSS: '',
	mainClass: '',
	uniqueClass: '',
}

export default PanelCustomCSSSettings

/**
 * If we were to implement LESS & SCSS compilers, use
 * https://github.com/Automattic/jetpack/tree/master/modules/custom-css/custom-css/preprocessors
 * See https://github.com/Automattic/jetpack/blob/f8078c2cd12ac508334da2fb08e37a92cf283c14/modules/custom-css/custom-css/preprocessors.php
 */
