/**
 * Contains deprecated code that's preserved for block migration.
 */
/**
 * Internal dependencies
 */
import { layer2s, layer3s } from './deprecated/layers_1_15'

/**
 * WordPress dependencies
 */
import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'

const layer2Output = ( output, design, props ) => {
	const {
		layer2 = false,
		layer2Color = '',
		layer2Height = 1,
		layer2Width = 1,
		layer2Flip = false,
		layer2Opacity = 0.5,
	} = props.attributes

	if ( layer2 ) {
		const styles = {
			transform: '',
			fill: layer2Color ? layer2Color : undefined,
			opacity: layer2Opacity,
		}

		styles.transform += layer2Width ? ` scaleX(${ layer2Width })` : ''
		styles.transform += layer2Flip ? ` scaleX(-1)` : ''
		styles.transform += layer2Height ? ` scaleY(${ layer2Height })` : ''

		const LayerSVG = layer2s[ design ]

		return (
			<Fragment>
				{ output }
				<LayerSVG
					preserveAspectRatio="none"
					className="ugb-separator__layer-2"
					style={ styles }
				/>
			</Fragment>
		)
	}
	return output
}

addFilter( 'stackable.separator.save.output.layers_1_15', 'stackable/separator/layer2', layer2Output, 7 )

const layer3Output = ( output, design, props ) => {
	const {
		layer3 = false,
		layer3Color = '',
		layer3Height = 1,
		layer3Width = 1,
		layer3Flip = false,
		layer3Opacity = 0.5,
	} = props.attributes

	if ( layer3 ) {
		const styles = {
			transform: '',
			fill: layer3Color ? layer3Color : undefined,
			opacity: layer3Opacity,
		}

		styles.transform += layer3Width ? ` scaleX(${ layer3Width })` : ''
		styles.transform += layer3Flip ? ` scaleX(-1)` : ''
		styles.transform += layer3Height ? ` scaleY(${ layer3Height })` : ''

		const LayerSVG = layer3s[ design ]

		return (
			<Fragment>
				{ output }
				<LayerSVG
					preserveAspectRatio="none"
					className="ugb-separator__layer-3"
					style={ styles }
				/>
			</Fragment>
		)
	}
	return output
}

addFilter( 'stackable.separator.save.output.layers_1_15', 'stackable/separator/layer3', layer3Output, 8 )

addFilter( 'stackable.separator.save.output.before_1_15', 'stackable/separator', ( output, design, props ) => {
	const { customCSSCompiled = '' } = props.attributes
	if ( ! customCSSCompiled ) {
		return output
	}

	return (
		<Fragment>
			{ output }
			<style>{ customCSSCompiled }</style>
		</Fragment>
	)
} )
