/**
 * Compile premium code and register the premium blocks.
 *
 * This is the file that Webpack is compiling into editor_blocks_deprecated_v2__premium_only.js
 */
import './modules'
import './editor-mode'

/**
 * External dependencies
 */
import registerBlock from '~stackable/deprecated/v2/register-block'

// Import all index.js and register all the blocks found (if name & settings are exported by the script)
const importAllAndRegister = r => {
	r.keys().forEach( key => {
		const { name, settings } = r( key )
		return name && settings && registerBlock( name, settings )
	} )
}

importAllAndRegister( require.context( './block', true, /index\.js$/ ) )
