
/**
 * Internal dependencies
 */
import './custom-css'
import './deprecated'
import './style'
import PanelEffectsSettings from '../../../../components/panel-effects-settings'

/**
 * External dependencies
 */
import {
	i18n,
	isPro,
	planName,
} from 'stackable'
import {
	ColorPaletteControl,
	PanelAdvancedSettings,
	AdvancedRangeControl,
	ImageSizeControl,
	ImageAltControl,
	ImageShapeControls,
} from '~stackable/components'
import { ResponsiveControl } from '../../../../../../src/deprecated/v2/components'
import { getImageUrlFromCache } from '~stackable/util'
import { showOptions } from '~stackable/deprecated/v2/block/pricing-box/util'
import { range } from 'lodash'

/**
 * WordPress dependencies
 */
import {
	__,
	_x,
	sprintf,
} from '@wordpress/i18n'
import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'

if ( isPro && planName !== 'starter' ) {
	addFilter( 'stackable.pricing-box.edit.inspector.style.before', 'stackable/pricing-box', ( output, props ) => {
		const { setAttributes } = props
		const { hoverEffect = '' } = props.attributes
		return (
			<Fragment>
				{ output }
				<PanelEffectsSettings
					hoverEffect={ hoverEffect }
					onChangeHoverEffect={ hoverEffect => setAttributes( { hoverEffect } ) }
				/>
			</Fragment>
		)
	}, 11 )

	// Per column colors.
	addFilter( 'stackable.pricing-box.edit.inspector.advanced.before', 'stackable/pricing-box', ( output, props ) => {
		const { setAttributes } = props
		const {
			columns = 3,
			showImage = true,
		} = props.attributes

		const show = showOptions( props )

		return (
			<Fragment>
				{ output }
				{ show.columnBackground && range( 1, columns + 1 ).map( i => (
					<PanelAdvancedSettings
						title={ sprintf( _x( '%s #%d', 'Panel title', i18n ), __( 'Column', i18n ), i ) }
						initialOpen={ false }
						key={ i }
					>
						<p className="components-base-control__help">{ sprintf( __( 'Override settings for column %d', i18n ), i ) }</p>
						{ show.columnBackground &&
							<ColorPaletteControl
								value={ props.attributes[ `column${ i }BackgroundColor` ] || '' }
								onChange={ value => setAttributes( { [ `column${ i }BackgroundColor` ]: value } ) }
								label={ __( 'Column Background', i18n ) }
							/>
						}
					</PanelAdvancedSettings>
				) ) }
				{ showImage && range( 1, columns + 1 ).map( i => {
					return show.imageSettings && (
						<PanelAdvancedSettings
							title={ sprintf( _x( '%s #%d', 'Panel title', i18n ), __( 'Image', i18n ), i ) }
							initialOpen={ false }
							key={ i }
						>
							<ImageAltControl
								value={ props.attributes[ `image${ i }Alt` ] }
								onChange={ value => setAttributes( { [ `image${ i }Alt` ]: value } ) }
							/>
							<ImageShapeControls
								imageId={ props.attributes[ `image${ i }Id` ] }
								imageSize={ props.attributes[ `image${ i }Size` ] }
								shape={ props.attributes[ `image${ i }Shape` ] || props.attributes.imageShape }
								shapeFlipX={ props.attributes[ `image${ i }ShapeFlipX` ] }
								shapeFlipY={ props.attributes[ `image${ i }ShapeFlipY` ] }
								shapeStretch={ props.attributes[ `image${ i }ShapeStretch` ] }
								onChangeShape={ value => setAttributes( { [ `image${ i }Shape` ]: value } ) }
								onChangeShapeFlipX={ value => setAttributes( { [ `image${ i }ShapeFlipX` ]: value } ) }
								onChangeShapeFlipY={ value => setAttributes( { [ `image${ i }ShapeFlipY` ]: value } ) }
								onChangeShapeStretch={ value => setAttributes( { [ `image${ i }ShapeStretch` ]: value } ) }
							/>
						</PanelAdvancedSettings>
					)
				} ) }
			</Fragment>
		)
	}, 19 )

	addFilter( 'stackable.pricing-box.edit.inspector.style.general.after', 'stackable/pricing-box', ( output, props ) => {
		const { setAttributes } = props
		const {
			columns = 3,
			design = 'basic',
		} = props.attributes

		return (
			<Fragment>
				{ output }
				{ ( design === 'colored' || design === 'sectioned' ) &&
					<PanelAdvancedSettings
						title={ __( 'Column Header', i18n ) }
						id="column-header"
						initialOpen={ false }
					>
						{ range( 1, columns + 1 ).map( i => (
							<ColorPaletteControl
								key={ i }
								value={ props.attributes[ `column${ i }HeaderBackgroundColor` ] || '' }
								onChange={ value => setAttributes( { [ `column${ i }HeaderBackgroundColor` ]: value } ) }
								label={ sprintf( _x( '%s #%d', 'Nth Title', i18n ), __( 'Column Header', i18n ), i ) }
							/>
						) ) }
					</PanelAdvancedSettings>
				}
			</Fragment>
		)
	} )

	addFilter( 'stackable.pricing-box.edit.inspector.style.column.after', 'stackable/pricing-box', ( output, props ) => {
		const { setAttributes } = props
		const {
			design = 'basic',
			showImage = true,
			imageSize = '',
			image1Id = '',
			image2Id = '',
			image3Id = '',
		} = props.attributes

		return (
			<Fragment>
				{ output }
				{ design === 'sectioned' &&
					<PanelAdvancedSettings
						title={ __( 'Image', i18n ) }
						checked={ showImage }
						onChange={ showImage => setAttributes( { showImage } ) }
						toggleOnSetAttributes={ [
							'imageSize',
							'imageHeight',
						] }
						toggleAttributeName="showImage"
					>
						<ImageSizeControl
							label={ __( 'Image Size', i18n ) }
							value={ imageSize }
							onChange={ size => {
								setAttributes( {
									imageSize: size,
									image1Url: getImageUrlFromCache( image1Id, size || 'medium' ),
									image2Url: getImageUrlFromCache( image2Id, size || 'medium' ),
									image3Url: getImageUrlFromCache( image3Id, size || 'medium' ),
								} )
							} }
						/>
						<ResponsiveControl
							attrNameTemplate="image%sHeight"
							setAttributes={ setAttributes }
							blockAttributes={ props.attributes }
						>
							<AdvancedRangeControl
								label={ __( 'Image Height', i18n ) }
								min={ 0 }
								max={ 400 }
								allowReset={ true }
								placeholder="100"
								className="ugb--help-tip-image-height-crop"
							/>
						</ResponsiveControl>
					</PanelAdvancedSettings>
				}
			</Fragment>
		)
	} )
}

addFilter( 'stackable.pricing-box.edit.content', 'stackable/pricing-box', ( output, props, comps ) => {
	const {
		design = 'basic',
		showImage = true,
		showTitle = true,
		showPrice = true,
		// showPricePrefix = true,
		// showPriceSuffix = true,
		showSubPrice = true,
		showButton = true,
		showDescription = true,
	} = props.attributes
	if ( design === 'compact' ) {
		return (
			<Fragment>
				<div className="ugb-pricing-box__header">
					{ showImage && comps.imageComp }
					<div className="ugb-pricing-box__side">
						{ showTitle && comps.titleComp }
						{ showPrice && comps.priceComp }
						{ showSubPrice && comps.subPriceComp }
					</div>
				</div>
				{ showDescription && comps.descriptionComp }
				{ showButton && comps.buttonComp }
			</Fragment>
		)
	} else if ( design === 'colored' ) {
		return (
			<Fragment>
				<div className="ugb-pricing-box__header">
					{ showImage && comps.imageComp }
					{ showTitle && comps.titleComp }
					{ showPrice && comps.priceComp }
					{ showSubPrice && comps.subPriceComp }
					{ showButton && comps.buttonComp }
				</div>
				{ showDescription &&
					<div className="ugb-pricing-box__footer">
						{ comps.descriptionComp }
					</div>
				}
			</Fragment>
		)
	} else if ( design === 'sectioned' ) {
		return (
			<Fragment>
				{ showTitle && comps.titleComp }
				{ showImage && comps.imageBgComp }
				{ showPrice && comps.priceComp }
				{ showSubPrice && comps.subPriceComp }
				{ showDescription && comps.descriptionComp }
				{ showButton && comps.buttonComp }
			</Fragment>
		)
	}
	return output
} )

