/**
 * External dependencies
 */
import { showOptions } from '~stackable/deprecated/v2/block/blog-posts/util'
import DivBackground from '../../../../../../src/deprecated/v2/components/div-background'

/**
 * WordPress dependencies
 */
import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'

addFilter( 'stackable.blog-posts.edit.output', 'stackable/blog-post/design/image-card', ( output, props, comps, i ) => {
	const {
		design = 'basic',
		contentOrder = '',
		showImage = true,
		showMeta = true,
		showTitle = true,
		showExcerpt = true,
		showCategory = true,
		showReadmore = false,
	} = props.attributes

	if ( design !== 'image-card' ) {
		return output
	}

	const show = showOptions( props )

	const content = {
		meta: showMeta && comps.meta,
		title: showTitle && comps.title,
		excerpt: ( showExcerpt || showReadmore ) && (
			<Fragment>
				{ showExcerpt && comps.excerpt }
				{ showReadmore && comps.readmore }
			</Fragment>
		),
		category: showCategory && comps.category,
	}

	// Default meta,title,excerpt,category.
	const compsToDisplay = ( contentOrder || 'meta,title,excerpt,category' ).split( ',' )

	return (
		<article className={ comps.itemClasses } key={ i }>
			<div className="ugb-blog-posts__header">
				{ showImage && comps.featuredImageBackground }
				{ content[ compsToDisplay[ 0 ] ] }
				{ content[ compsToDisplay[ 1 ] ] }
			</div>
			<DivBackground
				className="ugb-blog-posts__content"
				backgroundAttrName="column%s"
				blockProps={ props }
				showBackground={ show.showBackgroundInContent }
				showBackgroundVideo={ false }
			>
				{ content[ compsToDisplay[ 2 ] ] }
				{ content[ compsToDisplay[ 3 ] ] }
			</DivBackground>
		</article>
	)
} )
