
/**
 * Internal dependencies
 */
import './custom-css'
import './deprecated'
import './style'

/**
 * External dependencies
 */
import {
	i18n, isPro, planName,
} from 'stackable'

/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n'
import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import EffectsSettings from '../../../../components/effects-settings'

if ( isPro && planName !== 'starter' ) {
	addFilter( 'stackable.image-box.edit.image-hover-effects', 'stackable/image-box', effects => {
		return [
			...effects,
			{ label: __( 'Tilt', i18n ), value: 'tilt' },
			{ label: __( 'Zoom & Tilt', i18n ), value: 'zoom-tilt' },
			{ label: __( 'Up', i18n ), value: 'up' },
			{ label: __( 'Down', i18n ), value: 'down' },
			{ label: __( 'Left', i18n ), value: 'left' },
			{ label: __( 'Right', i18n ), value: 'right' },
			{ label: __( 'Blur In', i18n ), value: 'blur-in' },
			{ label: __( 'Blur Out', i18n ), value: 'blur-out' },
			{ label: __( 'Grayscale In', i18n ), value: 'grayscale-in' },
			{ label: __( 'Grayscale Out', i18n ), value: 'grayscale-out' },
		]
	} )

	addFilter( 'stackable.image-box.edit.panel.image-hover-effects', 'stackable/image-box', ( output, props ) => {
		const { setAttributes } = props
		const { hoverEffect = '' } = props.attributes
		return (
			<Fragment>
				{ output }
				<EffectsSettings
					hoverEffectLabel={ __( 'Box Hover Effect', i18n ) }
					hoverEffect={ hoverEffect }
					onChangeHoverEffect={ hoverEffect => setAttributes( { hoverEffect } ) }
					className="ugb--help-tip-image-box-box-hover-effect"
				/>
			</Fragment>
		)
	} )
}

addFilter( 'stackable.image-box.itemclasses', 'stackable/image-box', ( classes, props ) => {
	const {
		hoverEffect = '',
	} = props.attributes

	const newClasses = {
		...classes,
		[ `ugb--hover-${ hoverEffect }` ]: hoverEffect,
	}

	return newClasses
} )
