/**
 * WordPress dependencies
 */
import { addFilter } from '@wordpress/hooks'

/**
 * External dependencies
 */
import deepmerge from 'deepmerge'

/**
 * Internal dependencies
 */
import {
	whiteIfDarkBlackIfLight,
	appendImportant,
	__getValue,
} from '~stackable/util'
import {
	createBackgroundStyleSet,
} from '~stackable/deprecated/v2/util'
import { showOptions } from '~stackable/deprecated/v2/block/accordion/util'

addFilter( 'stackable.accordion.styles', 'stackable/premium/accordion', ( styles, props ) => {
	const getValue = __getValue( props.attributes )

	const newStyles = [ styles ]

	const show = showOptions( props )

	const {
		design = 'basic',
	} = props.attributes

	// Border radius on the background image.
	if ( show.borderRadius && ( design === 'line-colored' || design === 'colored' ) ) {
		newStyles.push( {
			'.ugb-accordion__item': {
				borderRadius: getValue( 'borderRadius', '%spx !important' ),
			},
		} )
	}

	// Column Background Line-Colored.
	if ( design === 'line-colored' ) {
		const columnBackgroundOptions = {
			importantBackgroundColor: true,
		}

		newStyles.push( {
			...( show.containerBackground ? createBackgroundStyleSet( 'container%s', 'ugb-accordion.ugb-accordion--open .ugb-accordion__item', props.attributes, columnBackgroundOptions ) : {} ),
		} )

		const {
			containerBackgroundColor = '',
		} = props.attributes
		if ( show.containerBackground ) {
			newStyles.push( {
				'.ugb-accordion__title': {
					color: getValue( 'titleColor' ),
				},
				'.ugb-accordion__arrow': {
					fill: getValue( 'arrowColor' ),
				},
				[ '.ugb-accordion.ugb-accordion--open .ugb-accordion__title, ' +
				  '.ugb-accordion.ugb-accordion--open .ugb-accordion__arrow' ]: {
					color: whiteIfDarkBlackIfLight( '', containerBackgroundColor ),
					fill: whiteIfDarkBlackIfLight( '', containerBackgroundColor ),
				},
				[ '.ugb-accordion__content-inner, ' +
				  '.ugb-accordion__content-inner p, ' +
				  '.ugb-accordion__content-inner li, ' +
				  '.ugb-accordion__content-inner h1, ' +
				  '.ugb-accordion__content-inner h2, ' +
				  '.ugb-accordion__content-inner h3, ' +
				  '.ugb-accordion__content-inner h4, ' +
				  '.ugb-accordion__content-inner h5, ' +
				  '.ugb-accordion__content-inner h6' ]: {
					color: appendImportant( whiteIfDarkBlackIfLight( '', containerBackgroundColor ) ),
				},
			} )
		}
	}

	// Column Background Colored.
	if ( design === 'colored' ) {
		const columnBackgroundOptions = {
			importantBackgroundColor: true,
		}

		newStyles.push( {
			...( show.containerBackground ? createBackgroundStyleSet( 'container%s', 'ugb-accordion__item', props.attributes, columnBackgroundOptions ) : {} ),
		} )

		newStyles.push( {
			'.ugb-accordion:not(.ugb-accordion--open) .ugb-accordion__item': {
				background: appendImportant( getValue( 'containerClosedBackgroundColor' ) ),
			},
			'.ugb-accordion:not(.ugb-accordion--open) .ugb-accordion__item:before': {
				display: appendImportant( getValue( 'containerClosedBackgroundColor' ) ? 'none' : undefined ),
			},
		} )

		const {
			containerBackgroundColor = '',
		} = props.attributes
		if ( show.containerBackground ) {
			newStyles.push( {
				'.ugb-accordion__title': {
					color: getValue( 'titleColor' ) ? getValue( 'titleColor' ) : whiteIfDarkBlackIfLight( '', containerBackgroundColor ),
				},
				'.ugb-accordion__arrow': {
					fill: getValue( 'arrowColor' ) ? getValue( 'arrowColor' ) : whiteIfDarkBlackIfLight( '', containerBackgroundColor ),
				},
				[ '.ugb-accordion:not(.ugb-accordion--open) .ugb-accordion__title, ' +
				  '.ugb-accordion:not(.ugb-accordion--open) .ugb-accordion__arrow' ]: {
					color: whiteIfDarkBlackIfLight( '', getValue( 'containerClosedBackgroundColor' ) ),
					fill: whiteIfDarkBlackIfLight( '', getValue( 'containerClosedBackgroundColor' ) ),
				},
				[ '.ugb-accordion__content-inner, ' +
				  '.ugb-accordion__content-inner p, ' +
				  '.ugb-accordion__content-inner li, ' +
				  '.ugb-accordion__content-inner h1, ' +
				  '.ugb-accordion__content-inner h2, ' +
				  '.ugb-accordion__content-inner h3, ' +
				  '.ugb-accordion__content-inner h4, ' +
				  '.ugb-accordion__content-inner h5, ' +
				  '.ugb-accordion__content-inner h6' ]: {
					color: appendImportant( whiteIfDarkBlackIfLight( '', containerBackgroundColor ) ),
				},
			} )
		}
	}

	// Spacing.
	if ( show.containerBackground ) {
		newStyles.push( {
			'.ugb-accordion__item': {
				paddingTop: appendImportant( getValue( 'containerPaddingTop', '%spx' ) ),
				paddingRight: appendImportant( getValue( 'containerPaddingRight', '%spx' ) ),
				paddingBottom: appendImportant( getValue( 'containerPaddingBottom', '%spx' ) ),
				paddingLeft: appendImportant( getValue( 'containerPaddingLeft', '%spx' ) ),
			},
		} )
	}

	return newStyles.length === 1 ? newStyles[ 0 ] : deepmerge.all( newStyles )
}, 11 )
