/**
 * WordPress dependencies
 */
import { addFilter } from '@wordpress/hooks'

/**
 * External dependencies
 */
import deepmerge from 'deepmerge'
import { __getValue } from '~stackable/util'

addFilter( 'stackable.feature.styles', 'stackable/premium/feature', ( styles, props ) => {
	const getValue = __getValue( props.attributes )

	const newStyles = [ styles ]

	const {
		design = 'basic',
	} = props.attributes

	// Border radius on the background image.
	if ( design.match( /^overlap-bg/ ) ) {
		newStyles.push( {
			'.ugb-feature__image': {
				borderRadius: getValue( 'borderRadius', '%spx !important' ),
			},
		} )
	}

	if ( design.match( /^overlap/ ) ) { // show.containerWidth
		newStyles.push( {
			'.ugb-feature__content': {
				width: getValue( 'containerWidth', '%spx !important' ),
			},
			tablet: {
				'.ugb-feature__content': {
					width: getValue( 'containerTabletWidth', '%spx !important' ),
				},
			},
			mobile: {
				'.ugb-feature__content': {
					width: getValue( 'containerMobileWidth', '%spx !important' ),
				},
			},
		} )
	}
	if ( design.match( /^overlap(.*)?[2-5]$/ ) ) { // show.containerOffset
		if ( design.match( /^overlap[2-5]$/ ) ) {
			const rule = design.match( /^overlap[24]$/ ) ? 'paddingBottom' : 'paddingTop'
			newStyles.push( {
				desktopTablet: {
					'.ugb-feature__image-side': {
						[ rule ]: getValue( 'containerOffset', '%spx !important' ),
					},
				},
				tabletOnly: {
					'.ugb-feature__image-side': {
						[ rule ]: getValue( 'containerTabletOffset', '%spx !important' ),
					},
				},
				mobile: {
					'.ugb-feature__image-side': {
						[ rule ]: getValue( 'containerMobileOffset', '%spx !important' ),
					},
				},
			} )
		} else {
			const rule = design.match( /^overlap-bg[24]$/ ) ? 'marginBottom' : 'marginTop'
			const sign = design.match( /^overlap-bg[24]$/ ) ? '' : '-'
			const translateX = design.match( /^overlap-bg[45]$/ ) ? 'translateX(-50%%) ' : ''
			newStyles.push( {
				'.ugb-feature__item': {
					[ rule ]: getValue( 'containerOffset', '%spx !important' ),
				},
				'.ugb-feature__content': {
					transform: getValue( 'containerOffset', `${ translateX }translateY(${ sign }%spx) !important` ),
				},
				tablet: {
					'.ugb-feature__item': {
						[ rule ]: getValue( 'containerTabletOffset', '%spx !important' ),
					},
					'.ugb-feature__content': {
						transform: getValue( 'containerTabletOffset', `${ translateX }translateY(${ sign }%spx) !important` ),
					},
				},
				mobile: {
					'.ugb-feature__item': {
						[ rule ]: getValue( 'containerMobileOffset', '%spx !important' ),
					},
					'.ugb-feature__content': {
						transform: getValue( 'containerMobileOffset', `${ translateX }translateY(${ sign }%spx) !important` ),
					},
				},
			} )
		}
	}

	return newStyles.length === 1 ? newStyles[ 0 ] : deepmerge.all( newStyles )
}, 11 )
