
/**
 * Internal dependencies
 */
import './custom-css'
import './deprecated'
import PanelEffectsSettings from '../../../../components/panel-effects-settings'

/**
 * External dependencies
 */
import { isPro } from 'stackable'

/**
 * WordPress dependencies
 */
import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import { __getValue } from '~stackable/util'

if ( isPro ) {
	addFilter( 'stackable.cta.edit.inspector.style.before', 'stackable/cta', ( output, props ) => {
		const { setAttributes } = props
		const { hoverEffect = '' } = props.attributes
		return (
			<Fragment>
				{ output }
				<PanelEffectsSettings
					hoverEffect={ hoverEffect }
					onChangeHoverEffect={ hoverEffect => setAttributes( { hoverEffect } ) }
				/>
			</Fragment>
		)
	}, 11 )
}

addFilter( 'stackable.cta.boxclasses', 'stackable/cta', ( classes, design, props ) => {
	const { hoverEffect = '' } = props.attributes
	return {
		...classes, [ `ugb--hover-${ hoverEffect }` ]: hoverEffect,
	}
} )

const output = ( output, comps, props ) => {
	const {
		design = 'basic',
	} = props.attributes

	if ( design === 'horizontal-2' ) {
		return (
			<Fragment>
				<div className="ugb-cta__side">
					{ comps.title }
					{ comps.description }
				</div>
				{ comps.button }
			</Fragment>
		)
	} else if ( design === 'horizontal-3' || design === 'split-centered' ) {
		return (
			<Fragment>
				{ comps.title }
				<div className="ugb-cta__side">
					{ comps.description }
					{ comps.button }
				</div>
			</Fragment>
		)
	}

	return output
}

addFilter( 'stackable.cta.edit.output', 'stackable/cta', output )
addFilter( 'stackable.cta.save.output', 'stackable/cta', output )

addFilter( 'stackable.cta.advanced-column-spacing.vertical-align', 'stackable/cta', ( styles, props, verticalAlignSelector ) => {
	const getValue = __getValue( props.attributes )

	const {
		design = 'basic',
	} = props.attributes

	if ( ! [ 'basic', 'plain' ].includes( design ) ) {
		return {
			[ verticalAlignSelector ]: {
				alignItems: getValue( 'columnContentVerticalAlign' ),
			},
			tablet: {
				[ verticalAlignSelector ]: {
					alignItems: getValue( 'tabletColumnContentVerticalAlign' ),
				},
			},
			mobile: {
				[ verticalAlignSelector ]: {
					alignItems: getValue( 'mobileColumnContentVerticalAlign' ),
				},
			},
		}
	}

	return styles
} )
