
/**
 * Internal dependencies
 */
import './custom-css'
import './deprecated'
import './style'
import PanelEffectsSettings from '../../../../components/panel-effects-settings'

/**
 * External dependencies
 */
import {
	i18n, isPro, planName,
} from 'stackable'
import { showOptions } from '~stackable/deprecated/v2/block/team-member/util'
import { range } from 'lodash'
import {
	AdvancedToggleControl,
	ColorPaletteControl,
	ImageAltControl,
	ImageShapeControls,
	PanelAdvancedSettings,
} from '~stackable/components'

/**
 * WordPress dependencies
 */
import {
	__, _x, sprintf,
} from '@wordpress/i18n'
import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import { __getValue, appendImportant } from '~stackable/util'

if ( isPro && planName !== 'starter' ) {
	addFilter( 'stackable.team-member.edit.inspector.style.before', 'stackable/team-member', ( output, props ) => {
		const { setAttributes } = props
		const {
			hoverEffect = '',
			imageColorOnHover = '',
		} = props.attributes
		return (
			<Fragment>
				{ output }
				<PanelEffectsSettings
					hoverEffect={ hoverEffect }
					onChangeHoverEffect={ hoverEffect => setAttributes( { hoverEffect } ) }
				>
					<AdvancedToggleControl
						label={ __( 'Color on Hover', i18n ) }
						checked={ imageColorOnHover }
						onChange={ imageColorOnHover => setAttributes( { imageColorOnHover } ) }
					/>
				</PanelEffectsSettings>
			</Fragment>
		)
	}, 11 )

	addFilter( 'stackable.team-member.edit.inspector.advanced.before', 'stackable/team-member', ( output, props ) => {
		const { setAttributes } = props
		const {
			columns,
			showImage = true,
		} = props.attributes

		const show = showOptions( props )

		return (
			<Fragment>
				{ output }
				{ showImage && ! show.imageAsBackground && range( 1, columns + 1 ).map( i => {
					return (
						<PanelAdvancedSettings
							title={ sprintf( _x( '%s #%d', 'Panel title', i18n ), __( 'Image', i18n ), i ) }
							initialOpen={ false }
							key={ i }
						>
							<ImageAltControl
								value={ props.attributes[ `image${ i }Alt` ] }
								onChange={ value => setAttributes( { [ `image${ i }Alt` ]: value } ) }
							/>
							<ImageShapeControls
								imageId={ props.attributes[ `image${ i }Id` ] }
								imageSize={ props.attributes[ `image${ i }Size` ] }
								shape={ props.attributes[ `image${ i }Shape` ] || props.attributes.imageShape }
								shapeFlipX={ props.attributes[ `image${ i }ShapeFlipX` ] }
								shapeFlipY={ props.attributes[ `image${ i }ShapeFlipY` ] }
								shapeStretch={ props.attributes[ `image${ i }ShapeStretch` ] }
								onChangeShape={ value => setAttributes( { [ `image${ i }Shape` ]: value } ) }
								onChangeShapeFlipX={ value => setAttributes( { [ `image${ i }ShapeFlipX` ]: value } ) }
								onChangeShapeFlipY={ value => setAttributes( { [ `image${ i }ShapeFlipY` ]: value } ) }
								onChangeShapeStretch={ value => setAttributes( { [ `image${ i }ShapeStretch` ]: value } ) }
							/>
						</PanelAdvancedSettings>
					)
				} ) }
				{ show.columnBackground && range( 1, columns + 1 ).map( i => (
					<PanelAdvancedSettings
						title={ sprintf( _x( '%s #%d', 'Panel title', i18n ), __( 'Column', i18n ), i ) }
						initialOpen={ false }
						key={ i }
					>
						<p className="components-base-control__help">{ sprintf( __( 'Override settings for column %d', i18n ), i ) }</p>
						{ show.columnBackground &&
							<ColorPaletteControl
								value={ props.attributes[ `column${ i }BackgroundColor` ] || '' }
								onChange={ value => setAttributes( { [ `column${ i }BackgroundColor` ]: value } ) }
								label={ __( 'Column Background', i18n ) }
							/>
						}
					</PanelAdvancedSettings>
				) ) }
			</Fragment>
		)
	} )
}

addFilter( 'stackable.team-member.mainclasses', 'stackable/team-member', ( classes, props ) => {
	const {
		imageColorOnHover = false,
	} = props.attributes
	return {
		...classes,
		'ugb-team-member--color-on-hover': imageColorOnHover,
	}
} )

addFilter( 'stackable.team-member.itemclasses', 'stackable/team-member', ( classes, props ) => {
	const {
		hoverEffect = '',
	} = props.attributes

	return {
		...classes,
		[ `ugb--hover-${ hoverEffect }` ]: hoverEffect,
	}
} )

// Change styles for Advanced -> Column spacing -> Content vertical align for some layouts.
addFilter( 'stackable.team-member.advanced-column-spacing.vertical-align', 'stackable/team-member', ( styles, props, verticalAlignSelector ) => {
	const getValue = __getValue( props.attributes )

	const {
		design = 'basic',
	} = props.attributes

	if ( design === 'horizontal' ) {
		return {
			[ verticalAlignSelector ]: {
				alignItems: appendImportant( getValue( 'columnContentVerticalAlign' ) ),
			},
			tablet: {
				[ verticalAlignSelector ]: {
					alignItems: appendImportant( getValue( 'tabletColumnContentVerticalAlign' ) ),
				},
			},
			mobile: {
				[ verticalAlignSelector ]: {
					alignItems: appendImportant( getValue( 'mobileColumnContentVerticalAlign' ) ),
				},
			},
		}
	} else if ( design === 'half' ) {
		return {
			'.ugb-team-member__content': {
				justifyContent: appendImportant( getValue( 'columnContentVerticalAlign' ) ),
			},
			tablet: {
				'.ugb-team-member__content': {
					justifyContent: appendImportant( getValue( 'tabletColumnContentVerticalAlign' ) ),
				},
			},
			mobile: {
				'.ugb-team-member__content': {
					justifyContent: appendImportant( getValue( 'mobileColumnContentVerticalAlign' ) ),
				},
			},
		}
	}

	return styles
} )

// Disable Advanced -> Column spacing -> Content vertical align for some layouts.
addFilter( 'stackable.team-member.edit.advanced.column-spacing.content-vertical-align.show', 'stackable/team-member/content-vertical-align', ( show, blockProps ) => {
	const {
		design = 'basic',
	} = blockProps.attributes

	if ( design === 'overlay' || design === 'overlay-simple' ) {
		return false
	}
	return show
} )
