/**
 * WordPress dependencies
 */
import { addFilter } from '@wordpress/hooks'

/**
 * External dependencies
 */
import deepmerge from 'deepmerge'
import { range } from 'lodash'

/**
 * Internal dependencies
 */
import { showOptions } from '~stackable/deprecated/v2/block/count-up/util'
import {
	appendImportant, whiteIfDarkBlackIfLight, __getValue,
} from '~stackable/util'

addFilter( 'stackable.count-up.styles', 'stackable/premium/count-up', ( styles, props ) => {
	const getValue = __getValue( props.attributes )

	const newStyles = [ styles ]

	const {
		columns = 2,
		showIcon = false,
	} = props.attributes

	const show = showOptions( props )

	// Advanced individual column color styles.
	range( 1, columns + 1 ).forEach( i => {
		newStyles.push( {
			[ `.ugb-countup__item${ i }` ]: {
				backgroundColor: show.columnBackground ? appendImportant( getValue( `column${ i }BackgroundColor` ) ) : undefined,
			},
			[ `.ugb-countup__item${ i }:before` ]: {
				background: show.columnBackground ? appendImportant( getValue( `column${ i }BackgroundColor` ) ) : undefined,
			},
			[ `.ugb-countup__item${ i } .ugb-countup__icon svg:not(.ugb-custom-icon)` ]: {
				color: showIcon ? appendImportant( whiteIfDarkBlackIfLight( getValue( `column${ i }IconColor` ), show.columnBackground && getValue( `column${ i }BackgroundColor` ) ) ) : undefined,
			},
			[ `.ugb-countup__item${ i } .ugb-countup__counter, ` +
			  `.ugb-countup__item${ i } .ugb-countup__title, ` +
			  `.ugb-countup__item${ i } .ugb-countup__description` ]: {
				color: appendImportant( whiteIfDarkBlackIfLight( '', show.columnBackground && getValue( `column${ i }BackgroundColor` ) ) ),
			},
		} )
	} )

	return newStyles.length === 1 ? newStyles[ 0 ] : deepmerge.all( newStyles )
}, 11 )
