
/**
 * Internal dependencies
 */
import './deprecated'
import './custom-css'
import './style'

/**
 * External dependencies
 */
import {
	i18n, isPro, planName,
} from 'stackable'
import { showOptions } from '~stackable/deprecated/v2/block/number-box/util'
import { ColorPaletteControl, PanelAdvancedSettings } from '~stackable/components'
import { whiteIfDarkBlackIfLight, appendImportant } from '~stackable/util'
import { omit, range } from 'lodash'

/**
 * WordPress dependencies
 */
import {
	__,
	_x,
	sprintf,
} from '@wordpress/i18n'
import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'

if ( isPro && planName !== 'starter' ) {
	// Per column colors.
	addFilter( 'stackable.number-box.edit.inspector.advanced.before', 'stackable/number-box', ( output, props ) => {
		const { setAttributes } = props
		const {
			columns = 2,
			showNumber = true,
		} = props.attributes

		const show = showOptions( props )

		return (
			<Fragment>
				{ output }
				{ range( 1, columns + 1 ).map( i => {
					const attrName = attrNameTemplate => sprintf( attrNameTemplate, i )

					return (
						<PanelAdvancedSettings
							title={ sprintf( _x( '%s #%d', 'Panel title', i18n ), __( 'Column', i18n ), i ) }
							initialOpen={ false }
							key={ i }
						>
							<p className="components-base-control__help">{ sprintf( __( 'Override settings for column %d', i18n ), i ) }</p>
							{ show.backgroundColor && (
								<ColorPaletteControl
									label={ __( 'Column Background', i18n ) }
									value={ props.attributes[ attrName( 'column%sBackgroundColor' ) ] }
									onChange={ value => setAttributes( { [ attrName( 'column%sBackgroundColor' ) ]: value } ) }
								/>
							) }
							{ showNumber && ( show.numberBGColor || show.numberColor ) && (
								<ColorPaletteControl
									label={ __( 'Number Background', i18n ) }
									value={ props.attributes[ attrName( 'column%sNumberBackgroundColor' ) ] }
									onChange={ value => setAttributes( { [ attrName( 'column%sNumberBackgroundColor' ) ]: value } ) }
								/>
							) }
						</PanelAdvancedSettings>
					)
				} ) }
			</Fragment>
		)
	}, 19 )
}

addFilter( 'stackable.number-box.show', 'stackable/number-box', ( show, blockProps ) => {
	const {
		design = 'basic',
	} = blockProps.attributes
	if ( [ 'background', 'heading', 'heading2' ].includes( design ) ) {
		return {
			...show,
			numberBGColor: false,
			numberStyle: false,
			spacingNumber: false,
		}
	}
	if ( design === 'faded' ) {
		return {
			...show,
			spacingNumber: false,
			backgroundColor: false,
			borderRadius: false,
			shadow: false,
			numberBGColor: false,
			numberStyle: false,
			columnBackground: false,
		}
	}
	return show
} )

addFilter( 'stackable.number-box.mainclasses', 'stackable/number-box', ( classes, design, props ) => {
	const {
		contentAlign = '',
		tabletContentAlign = '',
		mobileContentAlign = '',
	} = props.attributes
	if ( design === 'heading' || design === 'heading2' || design === 'faded' ) {
		return {
			...classes,
			[ `ugb--content-align-${ contentAlign }` ]: contentAlign,
			[ `ugb--tablet-content-align-${ tabletContentAlign }` ]: tabletContentAlign,
			[ `ugb--mobile-content-align-${ mobileContentAlign }` ]: mobileContentAlign,
		}
	}
	return classes
} )

addFilter( 'stackable.number-box.boxclasses', 'stackable/number-box', ( classes, design, props ) => {
	const { shadow } = props.attributes
	if ( design === 'faded' ) {
		return omit( classes, [ `ugb--shadow-${ shadow }` ] )
	}
	return classes
} )

const addDesignStyles = ( styleObject, props ) => {
	const {
		design,
		numberColor,
		contentAlign = '',
		tabletContentAlign = '',
		mobileContentAlign = '',
		numberAlign = '',
		numberTabletAlign = '',
		numberMobileAlign = '',
		showNumber = true,
		columnBackgroundColor = '',
	} = props.attributes

	if ( [ 'background', 'heading', 'heading2' ].includes( design ) ) {
		styleObject[ '.ugb-number-box__number' ] = omit( {
			...styleObject[ '.ugb-number-box__number' ],
			color: numberColor ? appendImportant( numberColor ) : undefined,
		}, [ 'backgroundColor' ] )
	}

	if ( ! [ 'basic', 'plain' ].includes( design ) ) {
		styleObject[ '.ugb-number-box__number' ] = omit( styleObject[ '.ugb-number-box__number' ], [ 'marginLeft', 'marginRight' ] )
	}

	if ( [ 'background', 'heading' ].includes( design ) ) {
		styleObject[ '.ugb-number-box__number' ] = {
			...styleObject[ '.ugb-number-box__number' ],
			color: appendImportant( whiteIfDarkBlackIfLight( numberColor, columnBackgroundColor, 'rgba(255,255,255,.08)', 'rgba(0,0,0,.08)' ) ),
		}
	}

	// Override number textAlign since it's in the reverse.
	if ( design === 'heading2' ) {
		if ( ( contentAlign !== '' || tabletContentAlign !== '' || mobileContentAlign !== '' ) && ( numberAlign === '' && numberTabletAlign === '' && numberMobileAlign === '' ) ) {
			styleObject[ '.ugb-number-box__number' ] = omit( styleObject[ '.ugb-number-box__number' ], [ 'textAlign' ] )
		}
	}

	if ( design === 'faded' ) {
		styleObject[ '.ugb-number-box__number' ] = omit( styleObject[ '.ugb-number-box__number' ], [ 'backgroundColor' ] )
		styleObject[ '.ugb-number-box__item' ] = omit( styleObject[ '.ugb-number-box__item' ], [ 'borderRadius', 'backgroundColor' ] )
		if ( ! showNumber ) {
			styleObject[ '.ugb-number-box__item' ] = { ...styleObject[ '.ugb-number-box__item' ], paddingTop: 0 }
		}
	}

	return styleObject
}

addFilter( 'stackable.number-box.styles', 'stackable/number-box/design', addDesignStyles )

/**
 * If the design was switched to basic / plain, the number text may become too big. Adjust it to the same ratio.
 */
const designMax = 500
const basicMax = 150
const min = 40

addFilter( 'stackable.number-box.setAttributes', 'stackable/number-box/design', ( attributes, props ) => {
	if ( typeof attributes.design === 'undefined' ) {
		return attributes
	}

	// Don't do anything if the max value didn't change.
	if ( [ 'basic', 'plain' ].includes( attributes.design ) && [ 'basic', 'plain' ].includes( props.attributes.design ) ) {
		return attributes
	}
	if ( ! [ 'basic', 'plain' ].includes( attributes.design ) && ! [ 'basic', 'plain' ].includes( props.attributes.design ) ) {
		return attributes
	}

	// Change the vaues, retain the ratio of the value.
	[ 'numberFontSize', 'numberTabletFontSize', 'numberMobileFontSize' ].forEach( attrName => {
		if ( props.attributes[ attrName ] === '' ) {
			return
		}
		const currValue = parseInt( props.attributes[ attrName ], 10 )
		const valuePercentage = props.attributes.design === 'basic' || props.attributes.design === 'plain' ? ( currValue - min ) / ( basicMax - min ) : ( currValue - min ) / ( designMax - min )
		const newValue = attributes.design === 'basic' || attributes.design === 'plain' ? valuePercentage * ( basicMax - min ) : valuePercentage * ( designMax - min )
		attributes[ attrName ] = parseInt( newValue + min, 10 )
	} )

	return attributes
} )

