/**
 * WordPress dependencies
 */
import { addFilter } from '@wordpress/hooks'

/**
 * External dependencies
 */
import {
	whiteIfDarkBlackIfLight, appendImportant, __getValue,
} from '~stackable/util'
import deepmerge from 'deepmerge'
import { showOptions } from '~stackable/deprecated/v2/block/number-box/util'
import { range } from 'lodash'

addFilter( 'stackable.number-box.styles', 'stackable/premium/number-box', ( styles, props ) => {
	const getValue = __getValue( props.attributes )

	const show = showOptions( props )

	const {
		columns = 2,
	} = props.attributes

	const newStyles = [ styles ]

	// Advanced individual column color styles.
	range( 1, columns + 1 ).forEach( i => {
		newStyles.push( {
			[ `.ugb-number-box__item${ i }` ]: {
				backgroundColor: appendImportant( show.columnBackground && getValue( `column${ i }BackgroundColor` ) ),
			},
			[ `.ugb-number-box__item${ i }:before` ]: {
				background: appendImportant( show.columnBackground && getValue( `column${ i }BackgroundColor` ) ),
			},
			[ `.ugb-number-box__item${ i } .ugb-number-box__number` ]: {
				backgroundColor: show.numberBGColor ? appendImportant( getValue( `column${ i }NumberBackgroundColor` ) ) : undefined,
				color: appendImportant( whiteIfDarkBlackIfLight( '', show.numberBGColor && getValue( `column${ i }NumberBackgroundColor` ) ) ),
			},
			[ `.ugb-number-box__item${ i } .ugb-number-box__title, ` +
			  `.ugb-number-box__item${ i } .ugb-number-box__description` ]: {
				color: appendImportant( whiteIfDarkBlackIfLight( '', show.columnBackground && getValue( `column${ i }BackgroundColor` ) ) ),
			},
		} )
	} )

	// Column-specific number color for non-basic & non-plain.
	if ( show.numberColor && ! show.numberBGColor ) {
		range( 1, columns + 1 ).forEach( i => {
			newStyles.push( {
				[ `.ugb-number-box__item${ i } .ugb-number-box__number` ]: {
					color: appendImportant( getValue( `column${ i }NumberBackgroundColor` ) ),
				},
			} )
		} )
	}

	return newStyles.length === 1 ? newStyles[ 0 ] : deepmerge.all( newStyles )
}, 11 )
