/**
 * External dependencies
 */
import { omit } from 'lodash'

/**
 * WordPress dependencies
 */
import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'

addFilter( 'stackable.testimonial.mainclasses_1_17_3', 'stackable/testimonial', ( classes, design, props ) => {
	const { customCSSUniqueID = '' } = props.attributes
	return {
		...classes,
		[ customCSSUniqueID ]: customCSSUniqueID,
	}
} )

addFilter( 'stackable.testimonial.itemclasses_1_17_3', 'stackable/testimonial', ( classes, design, props ) => {
	const { shadow } = props.attributes
	if ( design === 'bubble' ) {
		return omit( classes, [ `ugb--shadow-${ shadow }` ] )
	}
	if ( design === 'background' ) {
		return {
			...classes,
			[ `ugb--shadow-${ shadow }` ]: true,
		}
	}
	return classes
} )

addFilter( 'stackable.testimonial.styles_1_17_3', 'stackable/testimonial', ( styles, design, props ) => {
	const { borderRadius, backgroundColor } = props.attributes
	if ( design === 'bubble' ) {
		styles.item = omit( styles.item, [ 'borderRadius', 'backgroundColor' ] )
		styles.bodyWrapper = {
			...styles.bodyWrapper,
			borderRadius: borderRadius ? borderRadius : undefined,
			backgroundColor: backgroundColor ? backgroundColor : undefined,
			color: backgroundColor ? backgroundColor : undefined,
		}
	}
	if ( design === 'background' ) {
		styles.item = {
			...styles.item,
			borderRadius,
		}
	}
	if ( design === 'vertical' || design === 'vertical-inverse' ) {
		styles.bodyWrapper = {
			...styles.bodyWrapper,
			backgroundColor: backgroundColor ? backgroundColor : undefined,
			color: backgroundColor ? backgroundColor : undefined,
		}
	}
	return styles
} )

addFilter( 'stackable.testimonial.save.output.before_1_17_3', 'stackable/testimonial', ( output, design, props ) => {
	const { customCSSCompiled = '' } = props.attributes
	if ( ! customCSSCompiled ) {
		return output
	}

	return (
		<Fragment>
			{ output }
			<style>{ customCSSCompiled }</style>
		</Fragment>
	)
} )

addFilter( 'stackable.testimonial.bodyclasses_1_17_3', 'stackable/testimonial', ( classes, design, props ) => {
	const { shadow } = props.attributes
	if ( design === 'bubble' ) {
		return {
			...classes,
			[ `ugb--shadow-${ shadow }` ]: true,
		}
	}
	return classes
} )
