/**
 * WordPress dependencies
 */
import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'

/**
 * External dependencies
 */
import { omit } from 'lodash'

addFilter( 'stackable.feature-grid.mainclasses_1_17_2', 'stackable/feature-grid', ( classes, design, props ) => {
	const { customCSSUniqueID = '' } = props.attributes
	return {
		...classes,
		[ customCSSUniqueID ]: customCSSUniqueID,
	}
} )

addFilter( 'stackable.feature-grid.edit.output.before_1_17_2', 'stackable/feature-grid', ( output, design, props ) => {
	const { customCSSCompiled = '' } = props.attributes
	if ( ! customCSSCompiled ) {
		return output
	}

	return (
		<Fragment>
			{ output }
			<style>{ customCSSCompiled }</style>
		</Fragment>
	)
} )

addFilter( 'stackable.feature-grid.itemclasses_1_17_2', 'stackable/feature-grid', ( classes, design, i, props ) => {
	const { shadow } = props.attributes

	if ( design === 'large-mid' ) {
		return {
			...classes,
			[ `ugb--shadow-${ shadow }` ]: i === 2,
			'ugb--shadow-0': i !== 2,
		}
	}
	return classes
} )

addFilter( 'stackable.feature-grid.itemclasses_1_17_2', 'stackable/feature-grid', ( classes, design, i, props ) => {
	const {
		hoverEffect = '',
	} = props.attributes

	return {
		...classes,
		[ `ugb--hover-${ hoverEffect }` ]: hoverEffect,
	}
} )

addFilter( 'stackable.feature-grid.itemstyles_1_17_2', 'stackable/feature-grid', ( styles, design ) => {
	if ( design === 'horizontal' ) {
		return {
			...styles,
			image: omit( styles.image, [ 'width' ] ),
		}
	}
	return styles
} )
