
/**
 * Internal dependencies
 */
import './custom-css'
import './deprecated'
import './style'

/**
 * WordPress dependencies
 */
import { addFilter } from '@wordpress/hooks'
import {
	__, _x, sprintf,
} from '@wordpress/i18n'
import { Fragment } from '@wordpress/element'

/**
 * External dependencies
 */
import {
	i18n, isPro, planName,
} from 'stackable'
import { showOptions } from '~stackable/deprecated/v2/block/count-up/util'
import { __getValue, appendImportant } from '~stackable/util'
import { ColorPaletteControl, PanelAdvancedSettings } from '~stackable/components'
import { range } from 'lodash'

if ( isPro && planName !== 'starter' ) {
	addFilter( `stackable.count-up.edit.inspector.advanced.before`, `stackable/count-up/column-colors`, ( output, props ) => {
		const { setAttributes } = props
		const {
			columns = 2,
			showIcon = false,
		} = props.attributes

		const show = showOptions( props )

		return (
			<Fragment>
				{ output }
				{ range( 1, columns + 1 ).map( i => {
					const attrName = attrNameTemplate => sprintf( attrNameTemplate, i )

					return (
						show.columnBackground &&
							<PanelAdvancedSettings
								key={ i }
								initialOpen={ false }
								title={ sprintf( _x( '%s #%d', 'Panel title', i18n ), __( 'Column', i18n ), i ) }
							>
								<p className="components-base-control__help">{ sprintf( __( 'Override settings for column %d', i18n ), i ) }</p>
								<ColorPaletteControl
									label={ __( 'Column Background', i18n ) }
									value={ props.attributes[ attrName( 'column%sBackgroundColor' ) ] }
									onChange={ value => setAttributes( { [ attrName( 'column%sBackgroundColor' ) ]: value } ) }
								/>
								{ showIcon && (
									<ColorPaletteControl
										label={ __( 'Icon', i18n ) }
										value={ props.attributes[ attrName( 'column%sIconColor' ) ] }
										onChange={ value => setAttributes( { [ attrName( 'column%sIconColor' ) ]: value } ) }
									/>
								) }
							</PanelAdvancedSettings>
					)
				} ) }
			</Fragment>
		)
	} )
}

const changeLayoutOutput = ( output, comps, i, props ) => {
	const {
		design,
	} = props.attributes

	if ( design === 'side' ) {
		return (
			<Fragment>
				{ comps.countComp }
				<div className="ugb-countup__side">
					{ comps.iconComp }
					{ comps.titleComp }
					{ comps.descriptionComp }
				</div>
			</Fragment>
		)
	} else if ( design === 'abstract' ) {
		return (
			<Fragment>
				{ comps.iconComp }
				{ comps.countComp }
				<div className="ugb-countup__side">
					{ comps.titleComp }
					{ comps.descriptionComp }
				</div>
			</Fragment>
		)
	}

	return output
}

addFilter( 'stackable.count-up.edit.output', 'stackable/count-up', changeLayoutOutput )
addFilter( 'stackable.count-up.save.output', 'stackable/count-up', changeLayoutOutput )

addFilter( 'stackable.count-up.boxclasses', 'stackable/count-up', ( classes, unused, props ) => {
	const {
		shadow = '',
		design = 'plain',
	} = props.attributes

	if ( design === 'boxed' ) {
		classes[ `ugb--shadow-${ shadow }` ] = shadow
	}

	return classes
} )

// Special rules for: Advanced -> Column spacing -> Content Vertical Alignment
addFilter( 'stackable.count-up.advanced-column-spacing.vertical-align', 'stackable/count-up', ( styles, props, verticalAlignSelector ) => {
	const getValue = __getValue( props.attributes )

	const {
		design = 'plain',
	} = props.attributes

	if ( design === 'side' ) {
		return {
			[ verticalAlignSelector ]: {
				alignItems: appendImportant( getValue( 'columnContentVerticalAlign' ) ),
			},
			tablet: {
				[ verticalAlignSelector ]: {
					alignItems: appendImportant( getValue( 'tabletColumnContentVerticalAlign' ) ),
				},
			},
			mobile: {
				[ verticalAlignSelector ]: {
					alignItems: appendImportant( getValue( 'mobileColumnContentVerticalAlign' ) ),
				},
			},
		}
	}

	return styles
} )
